import { useEffect, useState } from 'react';
import { browser_api } from '@app/browser_api';
import type { Storage } from 'webextension-polyfill';
import { MetaApiData } from '@app/storage/local/types/expirableTypes';
import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export const useContentMetaApiData = (): MetaApiData => {
  const [metaApiData, setMetaApiData] = useState<MetaApiData>(
    {} as MetaApiData,
  );

  useEffect(() => {
    getLocalStorageData(LocalStorageKey.MetaApiData).then((result) => {
      if (result.metaApiData) {
        setMetaApiData(result.metaApiData as MetaApiData);
      }
    });

    const listener = (changes: { [key: string]: Storage.StorageChange }) => {
      if (changes[LocalStorageKey.MetaApiData]) {
        const newValue = changes[LocalStorageKey.MetaApiData].newValue;
        setMetaApiData(newValue as MetaApiData);
      }
    };

    browser_api.storage.local.onChanged.addListener(listener);

    return () => {
      browser_api.storage.local.onChanged.removeListener(listener);
    };
  }, []);

  return metaApiData;
};
