import React, { memo, useMemo } from 'react';
import { PaymentMethods } from '../../../types/PaymentMethods';
import { PaymentButton } from './PaymentButton/ui/PaymentButton';
import styles from './PaymentButtonsSection.module.css';

import { PaymentMethod } from '@app/features/types/paymentMethodTypes';

interface PaymentButtonsSectionProps {
  onPaymentClick: (method: PaymentMethod) => void;
  selectedPaymentMethod?: PaymentMethod | null;
  availablePaymentMethods?: PaymentMethods;
  disabled?: boolean;
}

const RawPaymentButtonsSection: React.FC<PaymentButtonsSectionProps> = ({
  selectedPaymentMethod,
  availablePaymentMethods,
  disabled,
  onPaymentClick,
}) => {
  if (!availablePaymentMethods?.length) {
    return null;
  }

  return (
    <div className={styles.container}>
      {availablePaymentMethods.map((method) => (
        <PaymentButton
          disabled={disabled}
          isSelected={selectedPaymentMethod === method}
          key={method}
          method={method}
          onClick={onPaymentClick}
        />
      ))}
    </div>
  );
};

export const PaymentButtonsSection = memo(RawPaymentButtonsSection);
