import { supportedLngs } from '@app/i18n';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';

export const useLanguageToggler = () => {
  const { i18n } = useTranslation();
  const startLangIndex = supportedLngs.findIndex(
    (lang) => lang === i18n.language,
  );
  const [langIndex, setLangIndex] = useState(startLangIndex);

  const toggleLanguage = () => {
    if (langIndex < supportedLngs.length - 1) {
      i18n.changeLanguage(supportedLngs[langIndex + 1]);
      setLangIndex(langIndex + 1);
      return;
    }
    i18n.changeLanguage(supportedLngs[0]);
    setLangIndex(0);
  };

  return {
    toggleLanguage,
    language: i18n.language,
    languages: supportedLngs,
  };
};
