import { useCallback, useState } from 'react';

import { useLocation } from 'react-router-dom';
import { usePaymentMethods } from './usePaymentMethods';
import { usePublicOfferUrl } from './usePublicOfferUrl';
import { useNewPaymentLink } from './useNewPaymentLink';
import { Tariff } from '../../../../../../api/premium/types/Tariff';

import { PaymentMethod } from '@app/features/types/paymentMethodTypes';

export function usePaymentScreen() {
  const [selectedPaymentMethod, setSelectedPaymentMethod] =
    useState<PaymentMethod | null>(PaymentMethod.Sbp);

  const [error, setError] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);

  const location = useLocation();
  const selectedTariff = location.state?.selectedTariff as Tariff;
  const hasOneMonthDiscount = location.state?.hasOneMonthDiscount as boolean;

  const availablePaymentMethods = usePaymentMethods();
  const publicOfferUrl = usePublicOfferUrl();
  const getPaymentLink = useNewPaymentLink();

  const handlePaymentClick = useCallback(async () => {
    try {
      if (!selectedTariff || !selectedPaymentMethod || loading) {
        return;
      }

      // TODO We need to rewrite this part of the code after backend implementation - https://vpnn.youtrack.cloud/issue/FRO-577
      const defaultTariffName = Tariff.DefaultOneMonthOneX;

      const tariff =
        selectedTariff === defaultTariffName && hasOneMonthDiscount
          ? Tariff.Discount
          : selectedTariff;

      setLoading(true);

      const { paymentLink } = await getPaymentLink(
        selectedPaymentMethod,
        tariff,
      );

      window.open(paymentLink, '_blank');
    } catch (error) {
      setError((error as Error)?.message || 'Ошибка при оплате');
      console.error(error);
    } finally {
      setLoading(false);
    }
  }, [
    getPaymentLink,
    hasOneMonthDiscount,
    loading,
    selectedPaymentMethod,
    selectedTariff,
  ]);

  return {
    loading,
    error,
    selectedPaymentMethod,
    setSelectedPaymentMethod,
    availablePaymentMethods,
    publicOfferUrl,
    onPaymentClick: handlePaymentClick,
  };
}
