import clsx from 'clsx';
import CopyToClipboard from 'react-copy-to-clipboard';
import { useCopyId } from '../hooks/useCopyId';
import { Icon } from './Icon';

import styles from './CopyId.module.css';
import { useTranslation } from 'react-i18next';

type CopyIdProps = {
  userId: string;
  size?: 'small' | 'big';
  align?: 'middle' | 'right';
  iconPlace?: 'i-left' | 'i-right';
  className?: string;
};

export function CopyId({
  userId,
  size = 'small',
  align = 'middle',
  iconPlace = 'i-left',
  className,
}: CopyIdProps): JSX.Element {
  const { t } = useTranslation('shared', {
    keyPrefix: 'copy-id',
  });

  const { handleCopy, isCopied } = useCopyId();

  return (
    <>
      <CopyToClipboard text={userId} onCopy={handleCopy}>
        <div
          className={clsx(
            styles.wrapper,
            styles[size],
            styles[align],
            styles[iconPlace],
            className,
          )}
        >
          <span
            className={clsx(
              styles.id,
              { [styles.copied]: isCopied },
              styles[size],
              styles[align],
            )}
          >
            <span className={styles['id-label']}>ID:</span> {userId}
          </span>
          <span
            className={clsx(
              styles['copy-text'],
              {
                [styles.copied]: isCopied,
              },
              styles[size],
              styles[align],
            )}
          >
            {t('copied')}
          </span>
          <Icon
            className={clsx(
              styles.icon,
              { [styles.copied]: isCopied },
              styles[size],
              styles[iconPlace],
            )}
          />
        </div>
      </CopyToClipboard>
    </>
  );
}
