import { useLanguageToggler } from '../hooks/useLanguageToggler';
import { GlobeIcon } from './GlobeIcon/GlobeIcon';
import styles from './LanguageToggler.module.css';

export const LanguageToggler = (): JSX.Element | null => {
  const { toggleLanguage, language, languages } = useLanguageToggler();

  if (languages.length < 2) {
    return null;
  }
  return (
    <button className={styles.toggler} onClick={toggleLanguage}>
      <GlobeIcon />
      <span>{language}</span>
    </button>
  );
};
