import { useEffect, useState } from 'react';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '../../../app/storage/local/shared';
import { Alarms } from '@app/alarms/shared';
import { browser_api } from '@app/browser_api';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

type AppHookValues = {
  showIsProxyControlledByOtherExtensions: boolean;
  showIsAdBlockersWork: boolean;
  isBrowserError: boolean;
  handleProxyControlledByOtherExtensionsClick: () => Promise<void>;
  handleAdBlockersWorkClick: () => Promise<void>;
  handleBrowserErrorClick: () => Promise<void>;
};

export function useApp(): AppHookValues {
  const [
    showIsProxyControlledByOtherExtensions,
    setShowIsProxyControlledByOtherExtensions,
  ] = useState(false);
  const [showIsAdBlockersWork, setShowIsAdBlockersWorks] = useState(false);
  const [browserError, setBrowserError] = useState(false);

  useEffect(() => {
    async function checkIsProxyControlledByOtherExtensions(): Promise<void> {
      const { isProxyControlledByOtherExtensions } = await getLocalStorageData(
        LocalStorageKey.IsProxyControlledByOtherExtensions,
      );

      const warningAlarm = await browser_api.alarms.get(
        Alarms.ExtensionConflictWarningProxy,
      );

      setShowIsProxyControlledByOtherExtensions(
        Boolean(isProxyControlledByOtherExtensions) && !warningAlarm,
      );
    }

    void checkIsProxyControlledByOtherExtensions();
  }, []);

  useEffect(() => {
    async function checkIsAdBlockersWork(): Promise<void> {
      const { isAdBlockersWork } = await getLocalStorageData(
        LocalStorageKey.IsAdBlockersWork,
      );

      const warningAlarm = await browser_api.alarms.get(
        Alarms.ExtensionConflictWarningAdBlocker,
      );

      setShowIsAdBlockersWorks(Boolean(isAdBlockersWork) && !warningAlarm);
    }

    void checkIsAdBlockersWork();
  }, []);

  useEffect(() => {
    async function checkIsBrowserError(): Promise<void> {
      const { isBrowserError } = await getLocalStorageData(
        LocalStorageKey.IsBrowserError,
      );
      setBrowserError(Boolean(isBrowserError));
    }

    void checkIsBrowserError();
  }, []);

  async function handleProxyControlledByOtherExtensionsClick(): Promise<void> {
    await setLocalStorageData({ popupWasOpened: true });
    setShowIsProxyControlledByOtherExtensions(false);

    // Set up alarm to show the proxy warning again after 24 hours
    await browser_api.alarms.create(Alarms.ExtensionConflictWarningProxy, {
      periodInMinutes: 24 * 60, // 24 hours
    });
  }

  async function handleAdBlockersWorkClick(): Promise<void> {
    await setLocalStorageData({ popupWasOpened: true });
    setShowIsAdBlockersWorks(false);

    // Set up alarm to show the ad blocker warning again after 24 hours
    await browser_api.alarms.create(Alarms.ExtensionConflictWarningAdBlocker, {
      periodInMinutes: 24 * 60, // 24 hours
    });
  }

  async function handleBrowserErrorClick(): Promise<void> {
    await setLocalStorageData({ isBrowserError: false });
    setBrowserError(false);
  }

  return {
    showIsProxyControlledByOtherExtensions,
    showIsAdBlockersWork,
    isBrowserError: browserError,
    handleProxyControlledByOtherExtensionsClick,
    handleAdBlockersWorkClick,
    handleBrowserErrorClick,
  };
}
