import { Button } from '@popup/shared/components/Button';
import { clsx } from 'clsx';
import { PremiumMiniHeader } from '../../../shared/PremiumMiniHeader';
import { type CancelPage } from '../../types/CancelPage';
import styles from './Problem.module.css';
import { useTranslation } from 'react-i18next';

const SUPPORT_URL = process.env.SUPPORT_URL;

type ProblemProps = {
  page: CancelPage;
  onNext: () => void;
};

export function Problem({ onNext, page }: ProblemProps): JSX.Element | null {
  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'cancel-premium.problem',
  });

  return (
    <div
      className={clsx(styles.wrapper, { [styles.out]: page !== 'problem' })}
      data-attribute="problem"
    >
      <PremiumMiniHeader />
      <div className={styles.emoji}>🔧</div>
      <div className={styles.info}>
        <h3 className={styles.title}>{t('title')}</h3>
        <span className={styles.text}>{t('description')}</span>
        <br />
        <span className={styles.support}>{t('support')}</span>
      </div>
      <div className={styles.buttons}>
        <a href={SUPPORT_URL} target="_blank" rel="noreferrer">
          <Button variant="primary">{t('actions.contact')}</Button>
        </a>
        <Button onClick={onNext} variant="secondary">
          {t('actions.continue')}
        </Button>
      </div>
    </div>
  );
}
