import { Actions } from '@app/actions/shared';
import { CreateNewUserMessage, isMessageOfType } from '@app/messages/shared';
import { useCallback, useEffect, useState } from 'react';
import { browser_api } from '@app/browser_api';
import {
  deleteLocalStorageData,

} from '@app/storage/local/shared';


import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export function useHandlePremiumWithoutTelegram() {
  const [loading, setLoading] = useState(false);
  const [isSuccess, setSuccess] = useState(false);

  const handleClick = useCallback(
    async (
      isSaleActive: boolean = false,
      isGuestMode: boolean = false,
      isTrial: boolean = false,
    ) => {
      setLoading(true);
      await deleteLocalStorageData(LocalStorageKey.UserId);

      await browser_api.runtime.sendMessage<CreateNewUserMessage>({
        action: Actions.CreateNewUser,
        payload: {
          isSaleActive,
          isGuestMode,
          isTrial,
        },
      });
    },
    [],
  );

  useEffect(() => {
    const listener = (message: unknown) => {
      if (isMessageOfType(message, Actions.CreateNewUserSuccess)) {
        setLoading(false);
        setSuccess(true);
      }
    };

    browser_api.runtime.onMessage.addListener(listener);

    return () => {
      browser_api.runtime.onMessage.removeListener(listener);
    };
  }, []);

  return { handleClick, loading, isSuccess };
}
