import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  type PopupReadyMessage,
  type Message,
  type CreateNewUserSuccessMessage,
  isMessageOfType,
} from '../../../../app/messages/shared';
import { Actions } from '../../../../app/actions/shared';
import { RoutePath } from '../routes/types/RoutePath';
import { browser_api } from '@app/browser_api';
import type { Runtime } from 'webextension-polyfill';

export function useNavigateToTariffs(): void {
  const navigate = useNavigate();

  useEffect(() => {
    const handleMessage: Runtime.OnMessageListenerNoResponse = (message) => {
      if (
        isMessageOfType(message, Actions.NavigateToPayment) ||
        isMessageOfType(message, Actions.CreateNewUserSuccess)
      ) {
        const isGuestMode = !!(
          message.action === Actions.CreateNewUserSuccess &&
          message.payload?.isGuestMode
        );

        const isTrialMode = !!(
          message.action === Actions.CreateNewUserSuccess &&
          message.payload?.isTrial
        );

        if (isGuestMode || isTrialMode) {
          return;
        }

        navigate(RoutePath.Tariffs, {
          state: {
            hasOneMonthDiscount: message.payload?.isSaleActive ?? false,
          },
        });
      }
    };

    browser_api.runtime.onMessage.addListener(handleMessage);

    return () => {
      browser_api.runtime.onMessage.removeListener(handleMessage);
    };
  }, [navigate]);
}
