import { Button } from '@popup/shared/components/Button';
import { DebugButton } from '@popup/shared/components/DebugButton';
import styles from './Footer.module.css';
import { useTranslation } from 'react-i18next';
import { browser_api } from '@app/browser_api';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

const SUPPORT_URL = process.env.SUPPORT_URL;

export function Footer(): JSX.Element {
  const [deviceId] = useLocalStorage(LocalStorageKey.DeviceId);

  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'footer',
  });

  const version = browser_api.runtime.getManifest().version;

  const trimmedDeviceId = deviceId?.slice(0, 4);

  return (
    <div className={styles.links}>
      <span className={styles.version}>ver. {version}</span>
      <span className={styles.version}>d: {trimmedDeviceId}</span>
      <a
        href={SUPPORT_URL}
        target="_blank"
        rel="noreferrer"
        className={styles.link}
      >
        <Button variant="link">{t('support')}</Button>
      </a>
      <DebugButton />
    </div>
  );
}
