import { Actions } from '@app/actions/shared';
import { isMessageOfType, Message } from '@app/messages/shared';
import {
  CookieKey,
  removeCookieData,
  setCookieData,
} from '@app/storage/cookies/shared';
import {
  getLocalStorageData,
  setLocalStorageData,
  getSubscriptionInfo,
} from '@app/storage/local/shared';
import { BaseRoutePath } from '@popup/shared/routes/types/RoutePath';
import { wait } from '@popup/shared/utils/wait';
import {
  useCallback,
  useEffect,
  useLayoutEffect,
  useRef,
  useState,
  type ChangeEvent,
  type RefObject,
  type MouseEvent,
  FormEvent,
} from 'react';
import { useNavigate } from 'react-router-dom';
import { browser_api } from '@app/browser_api';


import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

type EnterIdFormHookValues = {
  enteredId: string;
  isError: boolean;
  isLoading: boolean;
  inputRef: RefObject<HTMLInputElement>;
  handleChange: (e: ChangeEvent<HTMLInputElement>) => void;
  handleSubmit: (
    e: MouseEvent<HTMLButtonElement> | FormEvent<HTMLFormElement>,
  ) => Promise<void>;
  clearError: () => void;
  isIdSettled: boolean;
};

export function useEnterIdForm(): EnterIdFormHookValues {
  const [enteredId, setEnteredId] = useState('');
  const [isError, setIsError] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [isIdSettled, setIsIdSettled] = useState(false);

  const navigate = useNavigate();

  const inputRef = useRef<HTMLInputElement>(null);

  const handleChange = useCallback((e: ChangeEvent<HTMLInputElement>): void => {
    setEnteredId(e.target.value);
  }, []);

  const handleSubmit = useCallback(
    async (
      event: MouseEvent<HTMLButtonElement> | FormEvent<HTMLFormElement>,
    ): Promise<void> => {
      event.preventDefault();

      if (enteredId.trim() === '') {
        setIsError(true);

        console.error('No entered id');
        return;
      }

      try {
        setIsError(false);
        setIsLoading(true);
        const userId = enteredId.trim();

        await setLocalStorageData({
          userId,
        });

        await setCookieData({
          userId,
        });

        const { currentState } = await getLocalStorageData(
          LocalStorageKey.CurrentState,
        );

        const [subscriptionInfo] = await Promise.all([
          getSubscriptionInfo(),
          wait(1500),
        ]);

        if (!subscriptionInfo) {
          console.error('No subscription info');
          return;
        }

        await setLocalStorageData({
          userId,
        });

        if (currentState === 'connected') {
          await wait(500);
        }
        setIsIdSettled(true);
      } catch (error) {
        console.error(error);
        await removeCookieData(CookieKey.UserId);
        setIsError(true);
      } finally {
        setIsLoading(false);
      }
    },
    [enteredId],
  );

  const clearError = useCallback(() => {
    setIsError(false);
  }, []);

  useLayoutEffect(() => {
    inputRef.current?.focus();
  }, []);

  useEffect(() => {
    const listener = async (message: unknown) => {
      if (isMessageOfType(message, Actions.OpenPopupChangeDevice)) {
        navigate(BaseRoutePath.ChangeDevice, { state: { userId: enteredId } });
      }
    };

    browser_api.runtime.onMessage.addListener(listener);

    return () => {
      browser_api.runtime.onMessage.removeListener(listener);
    };
  }, [enteredId, navigate]);

  return {
    enteredId,
    isError,
    isLoading,
    isIdSettled,
    inputRef,
    handleChange,
    handleSubmit,
    clearError,
  };
}
