import { useLocalStorage } from './useLocalStorage';
import { PaymentStatus } from '@app/storage/local/types/persistentTypes';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export function usePaymentStatus(): PaymentStatus | undefined {
  const [paymentStatus] = useLocalStorage(LocalStorageKey.Status);

  return paymentStatus ?? undefined;
}
