import { ActionButton } from '@popup/shared/components/ActionButton';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import { useTranslation } from 'react-i18next';

type ExitPopUpProps = {
  onClose: () => void;
};

export function ExitPopUp({ onClose }: ExitPopUpProps): JSX.Element {
  const { t } = useTranslation('shared', {
    keyPrefix: 'exit-popup',
  });

  const { handleLogout } = usePremiumData();

  return (
    <>
      <h3>{t('title')}</h3>
      <ActionButton onClick={handleLogout}>{t('action-yes')}</ActionButton>
      <ActionButton onClick={onClose}>{t('action-no')}</ActionButton>
    </>
  );
}
