import React, { memo } from 'react';
import styles from './DescriptionSection.module.css';
import { useTranslation } from 'react-i18next';

const RawDescriptionSection: React.FC = () => {
  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'payment.description',
  });

  return (
    <div className={styles.info}>
      <h3 className={styles.title}>{t('title')}</h3>
      <p className={styles.text}>{t('guarantee')}</p>
    </div>
  );
};

export const DescriptionSection = memo(RawDescriptionSection);
