import clsx from 'clsx';
import { memo, useCallback, useEffect, useMemo, useRef } from 'react';
import { Tariff } from '@api/premium/types/Tariff';
import { TariffData } from '@api/urls';

import styles from './TariffComponent.module.css';
import { useTranslation } from 'react-i18next';

type TariffProps = {
  tariff: TariffData;
  hasOneMonthDiscount: boolean;
  minMonthPrice: number;
  onCLickTariff: (tariff: TariffData) => void;
  selectedTariff?: TariffData;
};

function RawTariffComponent({
  tariff,
  hasOneMonthDiscount,
  selectedTariff,
  minMonthPrice,
  onCLickTariff,
}: TariffProps): JSX.Element {
  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'tariffs',
  });

  // TODO We need to rewrite this part of the code after backend implementation - https://vpnn.youtrack.cloud/issue/FRO-577
  const isDiscountTariff = useMemo(() => {
    const defaultTariffName = Tariff.DefaultOneMonthOneX;

    return tariff.tariff === defaultTariffName && hasOneMonthDiscount;
  }, [hasOneMonthDiscount, tariff.tariff]);

  const isAutoClickClicked = useRef(false);

  const handleClickTariff = useCallback(() => {
    onCLickTariff(tariff);
  }, [tariff, onCLickTariff]);

  useEffect(() => {
    // We need to select discount tariff automatically if it's discount tariff
    if (selectedTariff && isDiscountTariff && !isAutoClickClicked.current) {
      isAutoClickClicked.current = true;

      queueMicrotask(() => {
        onCLickTariff(tariff);
      });
    }
  }, [isDiscountTariff, onCLickTariff, selectedTariff, tariff]);

  const getTariffLocaleKey = () => {
    const tariffCode = tariff.tariff;
    const tariffCodeFragments = tariffCode.split('_');

    if (tariffCodeFragments.length <= 2) {
      return tariffCode;
    }
    return tariffCodeFragments.slice(0, 2).join('_');
  };

  return (
    <button
      key={tariff.period}
      onClick={handleClickTariff}
      className={clsx(styles.tariff, {
        [styles.selected]: selectedTariff?.period === tariff.period,
        [styles.featured]: tariff.monthPrice === minMonthPrice,
        [styles.discount]: isDiscountTariff,
      })}
      type="button"
    >
      <div className={clsx({ [styles['discount-price']]: isDiscountTariff })}>
        {isDiscountTariff && (
          <div className={styles['prev-price']}>{tariff.price}₽</div>
        )}
        {isDiscountTariff ? 99 : tariff.price} ₽ /{' '}
        {t(`periods.${getTariffLocaleKey()}`, {
          defaultValue: tariff.period,
        })}
      </div>
      {!isDiscountTariff && (
        <span className={styles['second-price']}>
          {`(${tariff.monthPrice}₽ / ${t('periods.month')})`}
        </span>
      )}
      {tariff.monthPrice === minMonthPrice ? (
        <span className={styles['accent-offer']}>{t('accent-offer')}</span>
      ) : null}
    </button>
  );
}

export const TariffComponent = memo(RawTariffComponent);
