import { ArrowLeftIcon } from '@popup/shared/icons/ArrowLeftIcon';
import styles from './BackButton.module.css';
import { useTranslation } from 'react-i18next';

type BackButtonProps = {
  onClick: () => void;
};

export function BackButton({ onClick }: BackButtonProps): JSX.Element {
  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'onboarding.enter-id-step',
  });

  return (
    <div className={styles.container} onClick={onClick}>
      <ArrowLeftIcon />
      <div className={styles.text}>{t('back-button')}</div>
    </div>
  );
}
