import clsx from 'clsx';

import styles from './ButtonLoader.module.css';

export type ButtonLoaderVariant = 'points' | 'circle';

type ButtonLoaderProps = {
  variant?: ButtonLoaderVariant;
  className?: string;
};

export function ButtonLoader({
  variant = 'points',
  className,
}: ButtonLoaderProps) {
  return <div className={clsx(styles[`loader-${variant}`], className)} />;
}
