import {
  getLocalStorageData,

} from '@app/storage/local/shared';
import { useEffect, useState } from 'react';


import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export const useConfigsBaseUrl = () => {
  const [configsUrl, setConfigsUrl] = useState(process.env.CONFIG_BASE_URL_1);

  useEffect(() => {
    const getConfigsBaseUrl = async () => {
      const { configsBaseUrl } = await getLocalStorageData(
        LocalStorageKey.ConfigsBaseUrl,
      );
      if (configsBaseUrl) {
        setConfigsUrl(configsBaseUrl);
      }
    };
    void getConfigsBaseUrl();
  }, []);

  return { configsUrl };
};
