import { browser_api } from '@app/browser_api';
import { useContentMetaApiData } from '@content/hooks/useContentMetaApiData';
import { useMemo } from 'react';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

export function useGiftAdvertisingContent() {
  const navigate = useNavigate();
  const { i18n } = useTranslation();

  const onClickHideScreen = async () => {
    navigate(-1);
  };

  const { giftAdvertisingAvailableDates } = useContentMetaApiData();

  const onClickTelegramButton = async () => {
    browser_api.tabs.create({
      url: `https://t.me/${process.env.TELEGRAM_URL}`,
    });
  };

  const validUntil = useMemo(() => {
    if (!giftAdvertisingAvailableDates || !giftAdvertisingAvailableDates[1]) {
      return '';
    }

    const date = new Date(giftAdvertisingAvailableDates[1]);

    return date.toLocaleDateString(i18n.language, {
      day: 'numeric',
      month: 'long',
    });
  }, [giftAdvertisingAvailableDates, i18n.language]);

  return { onClickHideScreen, onClickTelegramButton, validUntil };
}
