import React, { useRef, useState } from 'react';
import { useLocation } from 'react-router-dom';
import { CSSTransition, SwitchTransition } from 'react-transition-group';

import styles from './TransitionWrapper.module.css';

type TransitionWrapperProps = {
  children: React.ReactNode;
  header: React.ReactNode;
};

export function TransitionWrapper({
  children,
  header,
}: TransitionWrapperProps): JSX.Element {
  const [isReversed, setIsReversed] = useState(false);
  const location = useLocation();
  const ref = useRef(null);

  function handleClick(event: React.MouseEvent<HTMLDivElement>): void {
    const target = event.target as HTMLElement;

    if (
      target.closest('[data-close="close-icon"]') ||
      target.closest('[data-close="close-button"]')
    ) {
      setIsReversed(true);
    } else {
      setIsReversed(false);
    }
  }

  return (
    <div className={styles.wrapper} onMouseDown={handleClick}>
      <>{header}</>
      <SwitchTransition>
        <CSSTransition
          className={styles.content}
          key={location.key}
          unmountOnExit
          nodeRef={ref}
          timeout={350}
          classNames={{
            enter: isReversed
              ? styles['page-enter-reversed']
              : styles['page-enter'],
            enterActive: isReversed
              ? styles['page-enter-active-reversed']
              : styles['page-enter-active'],
            exit: isReversed
              ? styles['page-exit-reversed']
              : styles['page-exit'],
            exitActive: isReversed
              ? styles['page-exit-active-reversed']
              : styles['page-exit-active'],
          }}
        >
          <div ref={ref}>{children}</div>
        </CSSTransition>
      </SwitchTransition>
    </div>
  );
}
