import Logo from '@popup/project/youboost-neo/components/Onboarding/assets/logo/neo.png';
import { Button } from '@popup/shared/components/Button';
import { useHandlePremiumWithoutTelegram } from '@popup/shared/hooks/useHandlePremiumWithoutTelegram';
import { forwardRef, useCallback } from 'react';
import { Trans, useTranslation } from 'react-i18next';
import { StepProps } from '../../types';
import { Separator } from '../../../../shared/Separator/Separator';

import styles from './WelcomeStep.module.css';

export const WelcomeStep = forwardRef<HTMLDivElement, StepProps>(
  ({ onStepChange }, ref) => {
    const { t } = useTranslation('youboost-neo', {
      keyPrefix: 'onboarding.welcome-step',
    });

    const { handleClick: createNewUser, loading } =
      useHandlePremiumWithoutTelegram();

    const handleCreateNewUser = useCallback(() => {
      createNewUser(false, false, true);
    }, [createNewUser]);

    const handleEnterId = useCallback(async () => {
      await onStepChange(1);
    }, [onStepChange]);

    return (
      <div className={styles.container} ref={ref}>
        <div className={styles.content}>
          <div className={styles.logo}>
            <img src={Logo} alt="YouBoost NEO" />
          </div>
          <div className={styles.title}>
            <Trans t={t} i18nKey="title" />
          </div>
          <div className={styles.description}>
            <Trans
              t={t}
              i18nKey="description"
              components={{ highlight: <span className={styles.highlight} /> }}
            />
          </div>
        </div>
        <div className={styles.buttons}>
          <Button
            className={styles.button}
            variant="primary"
            onClick={handleCreateNewUser}
            disabled={loading}
            loading={loading}
          >
            {t('go-free')}
          </Button>
          <Separator />

          <Button
            className={styles.button}
            variant="secondary"
            onClick={handleEnterId}
          >
            {t('go-premium')}
          </Button>
        </div>
      </div>
    );
  },
);

WelcomeStep.displayName = 'WelcomeStep';
