import { RoutePath } from '@popup/project/youboost-neo/routes/types/RoutePath';
import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';
import clsx from 'clsx';
import { motion } from 'motion/react';
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { ModeSwitcherButton } from './ModeSwitcherButton';

import styles from './ModeSwitcher.module.css';
import { useTranslation } from 'react-i18next';

export type ModeSwitcherProps = {
  hasPremium: boolean;
  isYoutubeMode: boolean;
  onModeChange: (isYoutubeMode: boolean) => void;
};

export const ModeSwitcher: React.FC<ModeSwitcherProps> = ({
  hasPremium,
  isYoutubeMode,
  onModeChange,
}) => {
  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'root.mode-switcher',
  });

  const paymentStatus = usePaymentStatus();
  const navigate = useNavigate();
  const handleClick = (isYoutubeMode: boolean) => {
    if (paymentStatus !== 'Paid' && paymentStatus !== 'Trial') {
      navigate(RoutePath.AllInternetPromo);
      return;
    }
    onModeChange(isYoutubeMode);
  };
  return (
    <div className={clsx(styles.container)}>
      <ModeSwitcherButton
        label={t('first-button')}
        isActive={isYoutubeMode}
        onClick={() => handleClick(true)}
      />
      <ModeSwitcherButton
        label={t('second-button')}
        isActive={!isYoutubeMode}
        disabled={!hasPremium}
        onClick={() => handleClick(false)}
      />

      <motion.div
        className={clsx(styles.switcher, {
          [styles['switcher-on']]: !isYoutubeMode,
        })}
        layout
        transition={{
          type: 'spring',
          visualDuration: 0.2,
          bounce: 0.2,
        }}
      />
    </div>
  );
};
