import { clsx } from 'clsx';
import { forwardRef, type ButtonHTMLAttributes } from 'react';
import {
  ButtonLoader,
  ButtonLoaderVariant,
} from './components/ButtonLoader/ui/ButtonLoader';

import styles from './Button.module.css';

type ButtonProps = ButtonHTMLAttributes<HTMLButtonElement> & {
  variant?: 'primary' | 'primary-n' | 'secondary' | 'link';
  size?: 'default' | 'small';
  type?: 'button' | 'submit';
  sharp?: boolean;
  loading?: boolean;
  loaderVariant?: ButtonLoaderVariant;
  loaderClassName?: string;
};

export const Button = forwardRef<HTMLButtonElement, ButtonProps>(
  (
    {
      variant = 'primary',
      size = 'default',
      type = 'button',
      sharp = false,
      children,
      className,
      loading,
      loaderVariant,
      loaderClassName,
      ...rest
    },
    ref,
  ) => {
    return (
      <button
        ref={ref}
        className={clsx(
          styles.common,
          styles[variant],
          styles[size],
          sharp && styles.sharp,
          className,
        )}
        type={type === 'button' ? 'button' : 'submit'}
        {...rest}
        data-variant={variant}
      >
        {loading && (
          <ButtonLoader variant={loaderVariant} className={loaderClassName} />
        )}
        {children}
      </button>
    );
  },
);

Button.displayName = 'Button';
