import { type FetchTariffsResponse } from '@api/urls';
import { RoutePath } from '@popup/project/youboost-neo/routes/types/RoutePath';
import { Button } from '@popup/shared/components/Button';
import { CloseIcon } from '@popup/shared/components/CloseIcon';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { useAllTariffs } from '../hooks/useAllTariffs';
import { DeviceSelector } from './DeviceSelector';
import { TariffComponent } from './Tariff';
import styles from './AllTariffs.module.css';
import { PremiumMiniHeader } from '@popup/project/youboost-neo/components/shared/PremiumMiniHeader';
import { useMemo } from 'react';
import { useTranslation } from 'react-i18next';

type AllTariffsProps = {
  tariffs?: FetchTariffsResponse;
};

export function AllTariffs({ tariffs }: AllTariffsProps): JSX.Element | null {
  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'tariffs',
  });

  const {
    onClickTariff,
    minMonthPrice,
    selectedTariff,
    hasOneMonthDiscount,
    tariffDeviceLimits,
    tariffsByLimits,
    selectedDeviceLimit,
    onSelectTariffDeviceLimit,
  } = useAllTariffs(tariffs);

  const navigate = useNavigate();

  const handleClickPayPremium = () => {
    navigate(RoutePath.Payment, {
      state: { selectedTariff: selectedTariff?.tariff, hasOneMonthDiscount },
    });
  };

  const tariffsByLimit = useMemo(() => {
    if (!selectedDeviceLimit) {
      return [];
    }

    return tariffsByLimits[selectedDeviceLimit] || [];
  }, [selectedDeviceLimit, tariffsByLimits]);

  if (!tariffs) {
    return null;
  }

  return (
    <div className={`${styles.wrapper} premium`}>
      {tariffDeviceLimits.length === 1 && (
        <PremiumMiniHeader className={styles.header} />
      )}
      <div className={styles.info}>
        {tariffDeviceLimits.length > 1 && (
          <>
            <div className={styles['link-block']}>
              <div className={styles.title}>{t('title')}</div>
              <Link to={RoutePath.Root} tabIndex={-1} className={styles.link}>
                <CloseIcon />
              </Link>
            </div>
            <div className={styles.text}>{t('quantity-text')}</div>
            <DeviceSelector
              selectedDeviceLimit={selectedDeviceLimit}
              options={tariffDeviceLimits}
              onSelect={onSelectTariffDeviceLimit}
            />
          </>
        )}
        <div className={styles.title}>
          <>
            {tariffDeviceLimits.length > 1 ? '2. ' : ''}
            {t('instruction')}
          </>
        </div>
        <div className={styles.text}>{t('guarantee')}</div>
      </div>
      <div className={styles.tariffs}>
        {selectedDeviceLimit &&
          tariffsByLimit.map((tariff) => (
            <TariffComponent
              key={tariff.period}
              tariff={tariff}
              hasOneMonthDiscount={hasOneMonthDiscount}
              minMonthPrice={minMonthPrice}
              onCLickTariff={onClickTariff}
              selectedTariff={selectedTariff}
            />
          ))}
      </div>
      <div className={styles.buttons}>
        <Button
          disabled={!selectedTariff}
          variant="primary"
          onClick={handleClickPayPremium}
          size="small"
        >
          {t('action')}
        </Button>
      </div>
    </div>
  );
}
