import { Error } from '@popup/shared/components/errors/Error';
import { useTariffs } from '../hooks/useTariffs';
import { AllTariffs } from './AllTariffs';
import { Loader } from '@popup/shared/components/Loader';

import styles from './TariffsScreenContent.module.css';

export function TariffsScreenContent(): JSX.Element {
  const { isTariffError, isTariffsLoading, tariffs } = useTariffs();

  if (isTariffError) {
    return <Error />;
  }

  if (isTariffsLoading) {
    return <Loader className={styles.loader} />;
  }

  return <AllTariffs tariffs={tariffs} />;
}
