import { ButtonLoader } from '@popup/shared/components/Button/ui/components/ButtonLoader/ui/ButtonLoader';
import { PuzzleIcon } from '@popup/shared/icons/PuzzleIcon';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';

import styles from './CurrentTariffTag.module.css';

export function CurrentTariffTag(): JSX.Element {
  const { deviceLimit, activeDevices } = usePremiumData();

  return (
    <div className={styles.tariff}>
      <span
        className={styles.text}
      >{`${activeDevices || '-'}/${deviceLimit || '-'}`}</span>
      <PuzzleIcon className={styles.icon} />
    </div>
  );
}
