import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import { clsx } from 'clsx';
import { useConnectButton } from '../hooks/useConnectButton';
import { PauseIcon } from './PauseIcon';
import { PlayIcon } from './PlayIcon';
import { ErrorIcon } from './ErrorIcon';

import styles from './ConnectButton.module.css';
import { useTranslation } from 'react-i18next';

export const ConnectButton = ({
  onExpiredRoute,
  className,
  variant = 'default',
}: {
  onExpiredRoute?: string;
  className?: string;
  variant?: 'default' | 'twihd';
}): JSX.Element => {
  const { t } = useTranslation(variant !== 'default' ? variant : 'shared', {
    keyPrefix: 'connect-button',
  });

  const { handleClick, status } = useConnectButton({
    onExpiredRoute,
  });

  const paymentStatus = usePaymentStatus();
  const { hasPremium } = usePremiumData();

  const Icon = () => {
    if (status === 'connected') {
      return PauseIcon();
    }
    if (status === 'error') {
      return ErrorIcon();
    }
    return PlayIcon();
  };

  return (
    <div className={className}>
      <button
        className={clsx({
          [styles['connect-button']]: variant === 'default',
          [styles[`connect-button-${variant}`]]: variant !== 'default',
          [styles.enabled]: status === 'connected',
          [styles.connecting]: status === 'connecting',
          [styles.error]: status === 'error',
          [styles.disabled]:
            (paymentStatus === 'Expired' || paymentStatus === 'New') &&
            hasPremium,
        })}
        onClick={handleClick}
      >
        <div
          className={clsx(styles.content, {
            [styles['content']]: variant === 'default',
            [styles[`content-${variant}`]]: variant !== 'default',
          })}
        >
          <div
            className={clsx({
              [styles['icon']]: variant === 'default',
              [styles[`icon-${variant}`]]: variant !== 'default',
            })}
          >
            {Icon()}
          </div>
          {status === 'error' ? (
            <div className={styles['error-info']}>
              <span>{t(`status-${status}`)}</span>
              <br></br>
              <span className={styles['description']}>{t('try-in-5-min')}</span>
            </div>
          ) : (
            <span>{t(`status-${status}`)}</span>
          )}
        </div>
      </button>
    </div>
  );
};

ConnectButton.displayName = 'ConnectButton';
