import { createNewPaymentLink } from '@api/premium/createNewPaymentLink';
import { getCreateNewPaymentLinkUrl } from '@api/urls';
import { getMetaApiData } from '@app/storage/local/metaApiData';
import { Tariff } from '@api/premium/types/Tariff';
import { getLocalStorageData } from '@app/storage/local/shared';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

import { PaymentMethod } from '@app/features/types/paymentMethodTypes';

export function useNewPaymentLink() {
  async function getNewPaymentLink(
    paymentMethod: PaymentMethod,
    tariffName: Tariff,
  ) {
    const { apiBaseUrl } = await getMetaApiData();
    const { userId } = await getLocalStorageData([LocalStorageKey.UserId]);
    const url = getCreateNewPaymentLinkUrl(apiBaseUrl);
    const data = await createNewPaymentLink({
      url,
      userId,
      tariffName,
      paymentMethod,
    });

    return data;
  }

  return getNewPaymentLink;
}
