import {
  getLocalStorageData,
  setLocalStorageData,

} from '@app/storage/local/shared';
import { useFeedbackOverlay } from '@popup/shared/contexts/FeedbackOverlayContext';
import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { browser_api } from '@app/browser_api';
import { ConnectionState } from '@app/storage/local/types/persistentTypes';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

type ConnectToggleHookValues = {
  status: ConnectionState;
  handleClick: () => Promise<void>;
};

export function useConnectButton({
  onExpiredRoute,
}: {
  onExpiredRoute?: string;
} = {}): ConnectToggleHookValues {
  const [status, setStatus] = useState<ConnectionState>('disconnected');
  const { checkAndShowFeedbackOverlay } = useFeedbackOverlay();
  const paymentStatus = usePaymentStatus();
  const { hasPremium } = usePremiumData();
  const navigate = useNavigate();

  useEffect(() => {
    async function getStatus(): Promise<void> {
      const { currentState } = await getLocalStorageData(
        LocalStorageKey.CurrentState,
      );
      if (currentState) {
        setStatus(currentState);
      }
    }

    void getStatus();
  }, []);

  useEffect(() => {
    function listener(changes: {
      currentState?: { newValue: ConnectionState };
    }): void {
      if (changes.currentState) {
        setStatus(changes.currentState.newValue);
      }
    }

    browser_api.storage.onChanged.addListener(listener);
    return () => {
      browser_api.storage.onChanged.removeListener(listener);
    };
  }, []);

  async function handleClick(): Promise<void> {
    if (
      (paymentStatus === 'Expired' || paymentStatus === 'New') &&
      hasPremium &&
      onExpiredRoute
    ) {
      navigate(onExpiredRoute);
      return;
    }
    const { currentState, targetState } = await getLocalStorageData([
      LocalStorageKey.TargetState,
      LocalStorageKey.CurrentState,
    ]);

    if (!targetState || targetState === 'disconnected') {
      await setLocalStorageData({ targetState: 'connected' });
    } else {
      if (currentState === 'connected') {
        await checkAndShowFeedbackOverlay();
      }
      await setLocalStorageData({ targetState: 'disconnected' });
    }
  }

  return {
    status,
    handleClick,
  };
}
