import { useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { changeDevice } from '../api/changeDevice';
import { wait } from '@popup/shared/utils/wait';
import {
  getSubscriptionInfo,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';


import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

type ChangeDeviceHookValues = {
  handleChangeDevice: () => Promise<void>;
  handleCancelChangeDevice: () => Promise<void>;
  isChangeDeviceError: boolean;
  isChangeDeviceLoading: boolean;
};

type NavigationState = {
  userId: string;
};

export function useChangeDevice(route: string): ChangeDeviceHookValues {
  const [isChangeDeviceError, setIsChangeDeviceError] = useState(false);
  const [isChangeDeviceLoading, setIsChangeDeviceLoading] = useState(false);

  const navigate = useNavigate();
  const location = useLocation();
  const { handleLogout } = usePremiumData();

  async function handleChangeDevice(): Promise<void> {
    try {
      setIsChangeDeviceLoading(true);
      const { userId } = location.state as NavigationState;
      await Promise.all([wait(1500), changeDevice(userId)]);

      await getSubscriptionInfo();

      await setLocalStorageData({
        [LocalStorageKey.OnboardingScreenStatus]: 'done',
      });

      await setLocalStorageData({
        [LocalStorageKey.UserId]: userId,
        [LocalStorageKey.IsChangeDeviceError]: false,
      });

      navigate(route);
    } catch (error) {
      console.error(error);
      setIsChangeDeviceError(true);
    } finally {
      setIsChangeDeviceLoading(false);
    }
  }

  async function handleCancelChangeDevice(): Promise<void> {
    await handleLogout();
    navigate(route);
  }

  return {
    handleChangeDevice,
    handleCancelChangeDevice,
    isChangeDeviceError,
    isChangeDeviceLoading,
  };
}
