import { Error } from '@popup/shared/components/errors/Error';
import React from 'react';
import { Loader } from '../../shared/Loader';
import { PremiumMiniHeader } from '../../shared/PremiumMiniHeader';
import { usePaymentScreen } from '../hooks/usePaymentScreen';
import { ContinueSection } from './ContinueSection';
import { DescriptionSection } from './DescriptionSection';
import { PaymentButtonsSection } from './PaymentButtonsSection';
import { PublicOfferSection } from './PublicOfferSection';
import { Footer } from '../../shared/Footer';
import styles from './PaymentScreenContent.module.css';
import { useTranslation } from 'react-i18next';

export const PaymentScreenContent: React.FC = () => {
  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'payment',
  });

  const {
    loading,
    error,
    selectedPaymentMethod,
    availablePaymentMethods,
    setSelectedPaymentMethod,
    publicOfferUrl,
    onPaymentClick,
  } = usePaymentScreen();

  if (loading) {
    return <Loader className={styles.loader}>{t('loader-text')}</Loader>;
  }

  if (error) {
    return <Error footer={<Footer />} />;
  }

  return (
    <div className={`${styles.container} premium`}>
      <PremiumMiniHeader />
      <DescriptionSection />
      <PaymentButtonsSection
        disabled={loading}
        selectedPaymentMethod={selectedPaymentMethod}
        availablePaymentMethods={availablePaymentMethods}
        onPaymentClick={setSelectedPaymentMethod}
      />
      {publicOfferUrl && <PublicOfferSection publicOfferUrl={publicOfferUrl} />}
      <ContinueSection onPay={onPaymentClick} disabled={loading} />
    </div>
  );
};
