import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { RoutePath } from '../routes/types/RoutePath';
import { browser_api } from '@app/browser_api';
import {
  getLocalStorageData,

} from '@app/storage/local/shared';


import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export function useChangeDevice(): void {
  const navigate = useNavigate();

  useEffect(() => {
    async function handleDeviceChangeError(): Promise<void> {
      const { isChangeDeviceError, userId } = await getLocalStorageData([
        LocalStorageKey.IsChangeDeviceError,
        LocalStorageKey.UserId,
      ]);
      if (isChangeDeviceError) {
        navigate(RoutePath.ChangeDevice, {
          state: { userId },
        });
      }
    }

    void handleDeviceChangeError();
  }, [navigate]);

  useEffect(() => {
    async function listener(changes: {
      isChangeDeviceError?: { newValue?: boolean };
    }): Promise<void> {
      if (changes.isChangeDeviceError?.newValue) {
        const { userId } = await getLocalStorageData([LocalStorageKey.UserId]);

        navigate(RoutePath.ChangeDevice, {
          state: { userId },
        });
      }
    }

    browser_api.storage.onChanged.addListener(listener);
    return () => {
      browser_api.storage.onChanged.removeListener(listener);
    };
  }, [navigate]);
}
