import {
  setLocalStorageData,
} from '@app/storage/local/shared';

import { memo, useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { CSSTransition, SwitchTransition } from 'react-transition-group';

import { Footer } from '../../shared/Footer';
import { EnterIdStep } from './components/EnterIdStep/EnterIdStep';
import { WelcomeStep } from './components/WelcomeStep/WelcomeStep';
import { OnboardingStep, stepMap, stepMapReverse } from './constants';

import styles from './Onboarding.module.css';
import { OnboardingScreenStatus } from '@app/storage/local/types/persistentTypes';

function RawOnboarding({
  onboardingStatus,
}: {
  onboardingStatus: OnboardingScreenStatus;
}): JSX.Element {
  const [step, setStep] = useState(onboardingStatus);
  const [isReversed, setIsReversed] = useState(false);
  const [hasInteracted, setHasInteracted] = useState(false);

  const zeroRef = useRef<HTMLDivElement>(null);
  const oneRef = useRef<HTMLDivElement>(null);

  const stepRefs = useMemo(
    () => ({
      [OnboardingStep.StepZero]: zeroRef,
      [OnboardingStep.StepOne]: oneRef,
      [OnboardingStep.Done]: null,
    }),
    [],
  );

  const handleStepChange = useCallback(
    async (newStep: number) => {
      if (!hasInteracted) {
        setHasInteracted(true);
      }
      const nextStepNumber = newStep;
      const prevStepNumber = stepMapReverse[step];

      await setLocalStorageData({
        onboardingScreenStatus: stepMap[newStep],
      });

      setIsReversed(nextStepNumber < prevStepNumber);
      setStep(stepMap[newStep]);
    },
    [hasInteracted, step],
  );

  const animationClasses = useMemo(() => {
    if (hasInteracted) {
      return isReversed
        ? {
            enter: styles['page-enter-right'],
            enterActive: styles['page-enter-active-right'],
            exit: styles['page-exit-right'],
            exitActive: styles['page-exit-active-right'],
          }
        : {
            enter: styles['page-enter-left'],
            enterActive: styles['page-enter-active-left'],
            exit: styles['page-exit-left'],
            exitActive: styles['page-exit-active-left'],
          };
    }
    return {};
  }, [hasInteracted, isReversed]);

  useEffect(() => {
    if (onboardingStatus !== OnboardingStep.Done) {
      setStep(onboardingStatus);
    }
  }, [onboardingStatus]);

  return (
    <div className={`${styles.container} premium`}>
      <SwitchTransition>
        <CSSTransition
          key={step}
          nodeRef={stepRefs[step]}
          timeout={300}
          classNames={animationClasses}
          unmountOnExit
        >
          <div className={styles['steps-container']}>
            {step === OnboardingStep.StepZero && (
              <WelcomeStep ref={zeroRef} onStepChange={handleStepChange} />
            )}

            {step === OnboardingStep.StepOne && (
              <EnterIdStep ref={oneRef} onStepChange={handleStepChange} />
            )}
          </div>
        </CSSTransition>
      </SwitchTransition>
      <Footer />
    </div>
  );
}

export const Onboarding = memo(RawOnboarding);
