import { RoutePath } from '@popup/project/youboost-neo/routes/types/RoutePath';
import { CloseIcon } from '@popup/shared/components/CloseIcon';
import { CopyId } from '@popup/shared/components/CopyId';
import { ExitButton } from '@popup/shared/components/ExitButton';
import clsx from 'clsx';
import { Link } from 'react-router-dom';
import { PremiumTag } from '../../PremiumTag';

import styles from './PremiumMiniHeader.module.css';

type PremiumMiniHeaderProps = {
  className?: string;
  link?: string;
  userId?: string;
};
export function PremiumMiniHeader({
  className,
  link = RoutePath.Root,
  userId,
}: PremiumMiniHeaderProps): JSX.Element {
  return (
    <div className={clsx(styles.header, className)}>
      <PremiumTag />
      {userId ? (
        <div className={styles['user-data']}>
          <CopyId userId={userId} align="right" iconPlace="i-right" />
          <ExitButton />
        </div>
      ) : (
        <Link to={link} tabIndex={-1} className={styles.link}>
          <CloseIcon />
        </Link>
      )}
    </div>
  );
}
