import { Fragment } from 'react';
import clsx from 'clsx';
import { AdvCards } from './types/AdvCards';
import { SadIcon } from '@popup/shared/icons/SadIcon';
import { CheckIcon } from '@popup/shared/icons/CheckIcon';
import styles from './AdvCard.module.css';
import { WarningIcon } from '@popup/shared/icons/WarningIcon';
import { useTranslation } from 'react-i18next';

type AdvCardProps = {
  card: AdvCards;
};

export function AdvCard({ card }: AdvCardProps) {
  const { t } = useTranslation('youboost-neo');

  return (
    <div
      className={clsx(
        styles.card,
        card.size === 'big' && styles.cardBig,
        card.size === 'small' && styles.cardSmall,
        card.size === 'wide' && styles.cardWide,
      )}
      key={card.titleLocaleKey}
    >
      <div
        className={clsx(styles.cardImage, {
          [styles.wide]: card.size === 'wide',
        })}
      >
        <card.image />
      </div>
      <div className={styles.cardTitle}>
        {t(card.titleLocaleKey)
          .split('\n')
          .map((line, index) => (
            <Fragment key={line}>
              {line}
              {index !== t(card.titleLocaleKey).split('\n').length - 1 &&
              card.size !== 'wide' ? (
                <br />
              ) : (
                ' '
              )}
            </Fragment>
          ))}
      </div>
      <div
        className={clsx(styles.cardType, {
          [styles.unsuccess]: card.type === 'unsuccess',
          [styles.warn]: card.type === 'warn',
          [styles.fixed]: card.size !== 'wide',
        })}
      >
        {card.type === 'success' && <CheckIcon />}
        {card.type === 'unsuccess' && <SadIcon />}
        {card.type === 'warn' && <WarningIcon />}
      </div>
    </div>
  );
}
