import {
  DeviceLimit,
  TariffData,
  type FetchTariffsResponse,
} from '@api/urls';
import { usePremiumAnimation } from '@popup/shared/hooks/usePremiumAnimation';
import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { useLocation } from 'react-router-dom';

type AllTariffsHookValues = {
  selectedTariff: TariffData | undefined;
  selectedDeviceLimit: DeviceLimit | null;
  minMonthPrice: number;
  onClickTariff: (tariff: TariffData) => void;
  onSelectTariffDeviceLimit: (deviceLimit: DeviceLimit) => void;
  hasOneMonthDiscount: boolean;
  tariffDeviceLimits: DeviceLimit[];
  tariffsByLimits: Partial<Record<DeviceLimit, TariffData[]>>;
};

export function useAllTariffs(
  tariffs?: FetchTariffsResponse,
): AllTariffsHookValues {
  const [selectedTariff, setSelectedTariff] = useState<TariffData>();

  const [selectedDeviceLimit, setSelectedDeviceLimit] =
    useState<DeviceLimit | null>(null);

  const [tariffDeviceLimits, setTariffDeviceLimits] = useState<
    DeviceLimit[] | []
  >([]);

  const [tariffsByLimits, setTariffsByLimits] = useState<
    Partial<Record<DeviceLimit, TariffData[]>>
  >({});
  const isInitialTariffSelected = useRef(false);

  usePremiumAnimation();

  const location = useLocation();

  const hasOneMonthDiscount = useMemo(() => {
    const locationState = location.state as
      | {
          hasOneMonthDiscount: boolean;
        }
      | undefined;
    return Boolean(locationState?.hasOneMonthDiscount);
  }, [location?.state]);

  const minMonthPrice = useMemo(
    () =>
      selectedDeviceLimit && tariffsByLimits[selectedDeviceLimit]?.length
        ? Math.min(
            ...(tariffsByLimits[selectedDeviceLimit]?.map(
              (tariff) => tariff.monthPrice,
            ) || []),
          )
        : 0,
    [selectedDeviceLimit, tariffsByLimits],
  );

  const handleSelectTariffDeviceLimit = useCallback(
    (deviceLimit: DeviceLimit) => {
      setSelectedDeviceLimit(deviceLimit);
    },
    [],
  );

  useEffect(() => {
    if (!tariffs || isInitialTariffSelected.current) {
      return;
    }
    let deviceLimits: DeviceLimit[] = [];
    let tariffsByLimits: Partial<Record<DeviceLimit, TariffData[]>> = {};
    let smallestDeviceLimit: DeviceLimit | null = null;

    for (const tariff of tariffs) {
      deviceLimits.push(tariff.deviceLimit);

      if (tariff.monthPrice === minMonthPrice) {
        setSelectedTariff(tariff);
        isInitialTariffSelected.current = true;
      }

      if (!tariffsByLimits[tariff.deviceLimit]) {
        tariffsByLimits[tariff.deviceLimit] = [];
      }

      tariffsByLimits[tariff.deviceLimit]?.push(tariff);

      if (!smallestDeviceLimit || tariff.deviceLimit < smallestDeviceLimit) {
        smallestDeviceLimit = tariff.deviceLimit;
      }
    }

    setTariffDeviceLimits([...new Set([...deviceLimits])]);

    setTariffsByLimits(tariffsByLimits);

    setSelectedDeviceLimit(smallestDeviceLimit);
  }, [minMonthPrice, tariffs]);

  const handleClickTariff = useCallback((tariff: TariffData) => {
    setSelectedTariff(tariff);
  }, []);

  useEffect(() => {
    if (selectedDeviceLimit) {
      setSelectedTariff(
        tariffsByLimits[selectedDeviceLimit]?.find(
          (tariff) => tariff.monthPrice === minMonthPrice,
        ),
      );
    }
  }, [minMonthPrice, selectedDeviceLimit, tariffsByLimits]);

  return {
    selectedTariff,
    selectedDeviceLimit,
    minMonthPrice,
    onClickTariff: handleClickTariff,
    onSelectTariffDeviceLimit: handleSelectTariffDeviceLimit,
    hasOneMonthDiscount,
    tariffDeviceLimits,
    tariffsByLimits,
  };
}
