import React, { useLayoutEffect } from 'react';
import { RouterProvider } from 'react-router-dom';
import { getMetaApiData } from '@app/storage/local/metaApiData';
import { FeatureToggler } from '../../shared/components/FeatureToggler';
import { FeedbackOverlayContextProvider } from '../../shared/contexts/FeedbackOverlayContext';
import { useApp } from '../../shared/hooks/useApp';
import { ProxyControlledByOtherExtensions } from '@popup/shared/components/ProxyControlledByOtherExtensions/ProxyControlledByOtherExtensions';
import { ErrorBoundary } from '@popup/shared/components/errors/ErrorBoundary';
import { BrowserError } from '@popup/shared/components/errors/BrowserError';
import { OldVersionError } from '@popup/shared/components/errors/oldVersionError';
import { Error } from '@popup/shared/components/errors/Error';
import { Header } from './components/shared/Header';
import { ThemeContextProvider } from '@popup/shared/contexts/Theme';
import { useFetchSubscriptionInfo } from '@popup/shared/hooks/useFetchSubscriptionInfo';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import { useVersion } from '@popup/shared/hooks/useVersion';
import { router } from './routes/router';
import './styles/popup.css';
import './styles/variables.css';
import { Footer } from './components/shared/Footer';
import { useFeaturesList } from '@app/features/hooks/useFeaturesList';
import { Feature } from '@app/features/types/Feature';
import { useErrorScreen } from '@popup/shared/hooks/useErrorScreen';

export function App(): JSX.Element {
  const {
    handleBrowserErrorClick,
    handleProxyControlledByOtherExtensionsClick,
    isBrowserError,
    showIsProxyControlledByOtherExtensions,
    showIsAdBlockersWork,
    handleAdBlockersWorkClick,
  } = useApp();

  const { isError } = usePremiumData();
  const { isVersionOld } = useVersion();
  const { featuresList } = useFeaturesList();
  const { errorMessage, handleRetry } = useErrorScreen();

  useFetchSubscriptionInfo();

  useLayoutEffect(() => {
    getMetaApiData();
  }, []);

  if (isVersionOld) {
    return <OldVersionError header={<Header />} />;
  }

  if (errorMessage) {
    return <Error text={errorMessage} onClick={handleRetry} />;
  }

  if (isError) {
    return <Error code={1002} header={<Header />} footer={<Footer />} />;
  }

  if (isBrowserError) {
    return (
      <BrowserError onClick={handleBrowserErrorClick} header={<Header />} />
    );
  }

  if (
    showIsProxyControlledByOtherExtensions &&
    featuresList[Feature.ProxyControlledByOtherExtensionsVisibility]
  ) {
    return (
      <ProxyControlledByOtherExtensions
        onClick={handleProxyControlledByOtherExtensionsClick}
        conflictType="proxy"
      >
        <Header />
      </ProxyControlledByOtherExtensions>
    );
  }

  if (
    showIsAdBlockersWork &&
    featuresList[Feature.ProxyControlledByOtherExtensionsVisibility]
  ) {
    return (
      <ProxyControlledByOtherExtensions
        onClick={handleAdBlockersWorkClick}
        conflictType="adBlocker"
      >
        <Header />
      </ProxyControlledByOtherExtensions>
    );
  }

  return (
    <React.StrictMode>
      <ErrorBoundary footer={<Footer />}>
        <FeedbackOverlayContextProvider>
          <ThemeContextProvider>
            <RouterProvider router={router} />
            <FeatureToggler />
          </ThemeContextProvider>
        </FeedbackOverlayContextProvider>
      </ErrorBoundary>
    </React.StrictMode>
  );
}
