import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  type PopupReadyMessage,
  type Message,
  isMessageOfType,
} from '../../../../app/messages/shared';
import { Actions } from '../../../../app/actions/shared';
import { RoutePath } from '../routes/types/RoutePath';
import { browser_api } from '@app/browser_api';

export function useNavigateToPremiumInfo(): void {
  const navigate = useNavigate();

  useEffect(() => {
    browser_api.runtime.onMessage.addListener((message: unknown) => {
      if (isMessageOfType(message, Actions.NavigateToFreeTrial)) {
        navigate(RoutePath.PremiumInfo);
      }
    });
  }, [navigate]);
}
