import { CloseIcon } from '../../CloseIcon';
import { useFeedbackOverlay } from '../../../contexts/FeedbackOverlayContext';
import styles from './CloseButton.module.css';

export function CloseButton(): JSX.Element {
  const { closeFeedbackOverlay } = useFeedbackOverlay();

  return (
    <button
      type="button"
      className={styles['close-button']}
      onClick={closeFeedbackOverlay}
    >
      <CloseIcon />
    </button>
  );
}
