import { PuzzleIcon } from '@popup/shared/icons/PuzzleIcon';
import styles from './DeviceSelector.module.css';
import { DeviceLimit } from '@api/urls';
import clsx from 'clsx';

export const DeviceSelector = ({
  selectedDeviceLimit,
  options,
  onSelect,
}: {
  selectedDeviceLimit: DeviceLimit | null;
  options: DeviceLimit[];
  onSelect: (option: DeviceLimit) => void;
}) => {
  const handleSelect = (option: DeviceLimit) => {
    onSelect(option);
  };

  const selectedIndex = selectedDeviceLimit
    ? options.indexOf(selectedDeviceLimit)
    : 0;

  return (
    <div
      className={styles['selector-container']}
      style={{ '--options-count': options.length } as React.CSSProperties}
    >
      <div
        className={styles.slider}
        data-position={selectedIndex}
        style={{ '--position': selectedIndex } as React.CSSProperties}
      />
      {options.map((option) => (
        <button
          key={option}
          className={clsx(styles.option, {
            [styles.selected]: selectedDeviceLimit === option,
          })}
          onClick={() => handleSelect(option)}
        >
          {option}
          <PuzzleIcon
            className={clsx(styles.icon, {
              [styles.selected]: selectedDeviceLimit === option,
            })}
          />
        </button>
      ))}
    </div>
  );
};
