import { Button } from '@popup/shared/components/Button';
import { clsx } from 'clsx';
import { Link } from 'react-router-dom';
import { RoutePath } from '../../../../routes/types/RoutePath';
import { PremiumMiniHeader } from '../../../shared/PremiumMiniHeader';
import { type CancelPage } from '../../types/CancelPage';
import { SaleLogo } from '../../../shared/SaleLogo';
import { Trans, useTranslation } from 'react-i18next';

import styles from './Discount.module.css';

type DiscountProps = {
  page: CancelPage;
  onNext: () => void;
};

export function Discount({ onNext, page }: DiscountProps): JSX.Element | null {
  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'cancel-premium.discount',
  });

  return (
    <div
      className={clsx(styles.wrapper, {
        [styles['out-right']]: page === 'problem',
        [styles['out-left']]: page === 'sorry',
        [styles['out-delay']]: page !== 'sorry',
      })}
      data-attribute="discount"
    >
      <PremiumMiniHeader />
      <div className={styles.discount}>
        <SaleLogo />
      </div>
      <div className={styles.info}>
        <h3 className={styles.title}>{t('title')}</h3>
        <div className={styles.text}>
          <Trans
            t={t}
            i18nKey="description"
            components={{ strong: <span className={styles.strong} /> }}
          />
        </div>
      </div>
      <div className={styles.buttons}>
        <Link
          to={RoutePath.Tariffs}
          state={{ hasOneMonthDiscount: true }}
          tabIndex={-1}
        >
          <Button variant="primary">{t('actions.activate-discount')}</Button>
        </Link>
        <Button onClick={onNext} variant="secondary">
          {t('actions.continue')}
        </Button>
      </div>
    </div>
  );
}
