import { clsx } from 'clsx';

import styles from './Error.module.css';
import { useTranslation } from 'react-i18next';
import { browser_api } from '@app/browser_api';

type ErrorProps = {
  className?: string;
  code?: number;
  isLoading?: boolean;
  text?: string | null;
  textLink?: React.ReactNode;
  buttonText?: string;
  onClick?: () => void;
  header?: React.ReactNode;
  footer?: React.ReactNode;
};

export function Error({
  className,
  code,
  isLoading = false,
  text,
  textLink,
  buttonText,
  onClick,
  header,
  footer,
}: ErrorProps): JSX.Element {
  const { t } = useTranslation('shared', {
    keyPrefix: 'error',
  });

  const defaultText = t('default-text');
  const defaultButtonText = t('default-button-text');

  const handleClick = onClick ?? (() => browser_api.runtime.reload());

  return (
    <div className={clsx(styles.wrapper, className)}>
      {header && header}
      <div className={styles.error}>
        <div className={styles.emoji}>😢</div>
        <div>
          <h1>{t('title')}</h1>
          <p>
            {text || defaultText}
            {textLink && textLink}
          </p>
        </div>
        <button
          disabled={isLoading}
          type="button"
          className={styles.reload}
          onClick={handleClick}
        >
          <div className={styles['reload-content']}>
            {isLoading && <div className={styles.loader} />}
            {buttonText || defaultButtonText}
          </div>
        </button>
        {!!code && (
          <span className={styles.code}>
            {t('error-code')}: {code}
          </span>
        )}
      </div>
      {footer && footer}
    </div>
  );
}
