import { httpClient } from '../httpClient';
import {
  type CancelSubscriptionResponse,
  getCancelSubscriptionApiUrl,
} from '../urls';
import { getLocalStorageData } from '@app/storage/local/shared';
import { getMetaApiData } from '@app/storage/local/metaApiData';
import { HTTPError } from 'ky';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export async function cancelSubscription(): Promise<CancelSubscriptionResponse> {
  try {
    const { apiBaseUrl } = await getMetaApiData();
    const [{ userId }, { deviceId, deviceIp }] = await Promise.all([
      getLocalStorageData(LocalStorageKey.UserId),
      getLocalStorageData([LocalStorageKey.DeviceId, LocalStorageKey.DeviceIp]),
    ]);

    const cancelSubscriptionApiUrl = getCancelSubscriptionApiUrl(apiBaseUrl);
    return await httpClient
      .post<CancelSubscriptionResponse>(cancelSubscriptionApiUrl, {
        json: {
          userId,
          deviceId,
          deviceIp,
        },
      })
      .json();
  } catch (error) {
    if (error instanceof HTTPError) {
      if (error.response.status === 400) {
        const errorData = await error.response.json<{
          errors: { code: string; description: string }[];
        }>();

        throw new Error(errorData.errors[0]?.description);
      }
      if (error.response.status === 500) {
        throw new Error(
          'Что-то пошло не так. Мы уже знаем об этом и скоро все починим.',
        );
      }
    }
    if (error instanceof Error && error.message === 'Failed to fetch') {
      throw new Error(
        'Что-то пошло не так. Мы уже знаем об этом и скоро все починим.',
      );
    }
    throw new Error(
      'Что-то пошло не так. Мы уже знаем об этом и скоро все починим.',
    );
  }
}
