import {
  deleteLocalStorageData,
  getLocalStorageData,
} from '@app/storage/local/shared';
import { useCallback, useEffect, useState } from 'react';
import { browser_api } from '@app/browser_api';
import type { Storage } from 'webextension-polyfill';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export function useErrorScreen() {
  const [errorMessage, setErrorMessage] = useState<string>('');

  const handleRetry = useCallback(async () => {
    await deleteLocalStorageData([LocalStorageKey.ErrorMessage]);
  }, []);

  useEffect(() => {
    async function getErrorData() {
      const { errorMessage } = await getLocalStorageData(
        LocalStorageKey.ErrorMessage,
      );
      if (errorMessage) {
        setErrorMessage(errorMessage);
      }
    }

    void getErrorData();
  }, []);

  useEffect(() => {
    const listener = async (changes: Storage.StorageChange) => {
      const { errorMessage } = changes as {
        [LocalStorageKey.ErrorMessage]: { newValue: string };
      };

      if (errorMessage) {
        setErrorMessage(errorMessage.newValue);
      }
    };

    browser_api.storage.local.onChanged.addListener(listener);

    return () => {
      browser_api.storage.local.onChanged.removeListener(listener);
    };
  }, []);

  return {
    errorMessage,
    handleRetry,
  };
}
