import { httpClient } from '@api/httpClient';
import { getChangeDeviceApiUrl } from '@api/urls';
import { getMetaApiData } from '@app/storage/local/metaApiData';
import { getDeviceId, getDeviceIP } from '@background/client/utils';

export async function changeDevice(userId: string): Promise<void> {
  const { apiBaseUrl } = await getMetaApiData();

  const deviceId = await getDeviceId();
  const deviceIp = await getDeviceIP();

  const changeDeviceApiUrl = getChangeDeviceApiUrl(apiBaseUrl);
  await httpClient
    .post(changeDeviceApiUrl, {
      json: {
        userId,
        deviceId,
        deviceIp,
      },
    })
    .json();
}
