import React, { memo } from 'react';
import { Button } from '@popup/shared/components/Button';

import styles from './ContinueSection.module.css';
import { Trans, useTranslation } from 'react-i18next';

interface ContinueSectionProps {
  onPay: () => void;
  disabled?: boolean;
}

const RawContinueSection: React.FC<ContinueSectionProps> = ({
  disabled,
  onPay,
}) => {
  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'payment.continue',
  });

  return (
    <div className={styles.container}>
      <p className={styles.text}>
        <Trans t={t} i18nKey="text" />
      </p>
      <Button
        disabled={disabled}
        variant="primary"
        onClick={onPay}
        size="small"
      >
        {t('action')}
      </Button>
    </div>
  );
};

export const ContinueSection = memo(RawContinueSection);
