import i18n, { type Resource } from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
import en from '@locales/en';
import ru from '@locales/ru';
import { featureStore } from '@app/features/list';
import { Feature } from '@app/features/types/Feature';

const { featuresList } = await featureStore.getSnapshotAsync();
const hasMultiLocales = featuresList[Feature.MultiLocalesSupport];

export const supportedLngs = hasMultiLocales ? ['ru', 'en'] : ['ru'];

const resources: Resource = hasMultiLocales ? { ru, en } : { ru };

i18n.use(LanguageDetector).use(initReactI18next).init({
  supportedLngs,
  fallbackLng: supportedLngs,
  debug: true,
  defaultNS: 'shared',
  resources,
});

export default i18n;
