import { useEffect, useState } from 'react';
import { getMetaApiData } from '@app/storage/local/metaApiData';

export function usePublicOfferUrl() {
  const [publicOfferUrl, setPublicOfferUrl] = useState<string | null>(null);

  useEffect(() => {
    const getMetaData = async () => {
      const data = await getMetaApiData();
      if (!data.publicOfferUrl) {
        return;
      }
      setPublicOfferUrl(data.publicOfferUrl);
    };
    getMetaData();
  }, []);

  return publicOfferUrl;
}
