import React, { useCallback, useEffect, useState } from 'react';
import { Feature } from '../../../../../app/features/types/Feature';
import styles from './FeatureToggler.module.css';
import { featureStore } from '@app/features/list';
import { useFeaturesList } from '@app/features/hooks/useFeaturesList';
import { browser_api } from '@app/browser_api';

export const FeatureToggler: React.FC = () => {
  const { featuresList: features } = useFeaturesList();

  const [isVisible, setIsVisible] = useState(false);

  const toggleFeature = useCallback((key: Feature, isActive: boolean) => {
    featureStore.setFeatureValues({ [key]: isActive });
  }, []);

  useEffect(() => {
    Object.defineProperty(window, 'magicToggler', {
      configurable: true,
      get() {
        setIsVisible(true);
        return 'Feature toggler is now visible!';
      },
    });

    return () => {
      // @ts-ignore
      delete window.magicToggler;
    };
  }, []);

  const applyChanges = () => {
    browser_api.runtime.reload();
  };

  const handleReset = () => {
    featureStore.reset();
    browser_api.runtime.reload();
  };

  if (!isVisible || !features) {
    return null;
  }

  return (
    <div className={styles.overlay}>
      <div className={styles.container}>
        <h3>Feature Toggler</h3>
        {Object.entries(features).map(([feature, isActive]) => (
          <label key={feature} className={styles.item}>
            <input
              type="checkbox"
              checked={features[feature as Feature]}
              onChange={() => toggleFeature(feature as Feature, !isActive)}
            />
            {feature}
          </label>
        ))}
        <button className={styles['apply-button']} onClick={applyChanges}>
          Применить
        </button>
        <button className={styles['apply-button']} onClick={handleReset}>
          Сбросить
        </button>
      </div>
    </div>
  );
};
