import {
  fetchCheckTariffAvailability,
  setupTariff,
} from '@api/premium/fetchPremiumData';
import { Tariff } from '@api/premium/types/Tariff';
import {
  getLocalStorageData,
  getSubscriptionInfo,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { getMetaApiData } from '@app/storage/local/metaApiData';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { wait } from '@popup/shared/utils/wait';
import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

type FreeTrialHookValues = {
  isCheckingFreePremiumTrial: boolean;
  canTryFreePremiumTrial: boolean;
  handleClickTryPremium: () => Promise<void>;
  isFreePremiumTrialLoading: boolean;
  isFreePremiumTrialError: boolean;
};

export function useFreePremiumTrial(): FreeTrialHookValues {
  const [isChecking, setIsChecking] = useState(true);
  const [canTryTrial, setCanTryTrial] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [isError, setIsError] = useState(false);

  const navigate = useNavigate();

  useEffect(() => {
    async function checkFreePremiumTrial(): Promise<void> {
      try {
        const { isFreePremiumTrialAvailable } = await getLocalStorageData([
          LocalStorageKey.IsFreePremiumTrialAvailable,
        ]);

        if (
          isFreePremiumTrialAvailable !== undefined &&
          isFreePremiumTrialAvailable !== null
        ) {
          setCanTryTrial(isFreePremiumTrialAvailable);
          return;
        }

        const [{ apiBaseUrl }, { deviceId, deviceIp }, { userId }] =
          await Promise.all([
            getMetaApiData(),
            getLocalStorageData([
              LocalStorageKey.DeviceId,
              LocalStorageKey.DeviceIp,
            ]),
            getLocalStorageData(LocalStorageKey.UserId),
          ]);
        if (userId && deviceId) {
          const response = await fetchCheckTariffAvailability(apiBaseUrl, {
            userId,
            deviceId,
            deviceIp,
            tariff: Tariff.FreeTrial,
          });

          if ('error' in response) {
            // TODO: replace error handling https://vpnn.youtrack.cloud/issue/FRO-552
            throw new Error(response.error);
          }

          const { status } = response;

          const isAvailable = status === 'allowed';
          await setLocalStorageData({
            isFreePremiumTrialAvailable: isAvailable,
          });
          setCanTryTrial(isAvailable);
        }
      } catch (error) {
        console.error(error);
        setIsError(true);
      } finally {
        setIsChecking(false);
      }
    }

    void checkFreePremiumTrial();
  }, []);

  async function handleClickTryPremium(): Promise<void> {
    try {
      setIsLoading(true);
      const [{ apiBaseUrl }, { deviceId, deviceIp, currentState }, { userId }] =
        await Promise.all([
          getMetaApiData(),
          getLocalStorageData([
            LocalStorageKey.DeviceId,
            LocalStorageKey.DeviceIp,
            LocalStorageKey.CurrentState,
          ]),
          getLocalStorageData(LocalStorageKey.UserId),
        ]);

      if (userId && deviceId && canTryTrial) {
        await setupTariff(apiBaseUrl, {
          userId,
          deviceId,
          deviceIp,
          tariff: Tariff.FreeTrial,
        });
        await setLocalStorageData({
          [LocalStorageKey.IsFreePremiumTrialAvailable]: false,
        });

        await getSubscriptionInfo();

        if (currentState === 'connected') {
          await wait(500);
        }

        navigate(RoutePath.Root);
      } else if (!userId) {
        await setLocalStorageData({ onboardingScreenStatus: 'step-zero' });
        navigate(RoutePath.Root);
      }
    } catch (error) {
      console.error(error);
      setIsError(true);
    } finally {
      setIsLoading(false);
    }
  }

  return {
    isCheckingFreePremiumTrial: isChecking,
    canTryFreePremiumTrial: canTryTrial,
    handleClickTryPremium,
    isFreePremiumTrialLoading: isLoading,
    isFreePremiumTrialError: isError,
  };
}
