import { AllInternetBenefit } from '../AllInternetBenefit';
import { AllInternetBenefitProps } from '../AllInternetBenefit/types/AllInternetBenefit';

import styles from './AllInternetBenefits.module.css';

type AllInternetBenefitsProps = {
  benefits: Array<AllInternetBenefitProps>;
};

export function AllInternetBenefits({
  benefits,
}: AllInternetBenefitsProps): JSX.Element {
  return (
    <div className={styles.benefits}>
      {benefits.map((benefit) => (
        <AllInternetBenefit
          titleLocaleKey={benefit.titleLocaleKey}
          image={benefit.image}
          key={benefit.titleLocaleKey}
        />
      ))}
    </div>
  );
}
