import { useContext } from 'react';
import {
  FeedbackOverlayContext,
  type FeedbackOverlayContextType,
} from '../FeedbackOverlayContext';

export function useFeedbackOverlay(): FeedbackOverlayContextType {
  const context = useContext(FeedbackOverlayContext);
  if (context === undefined) {
    throw new Error(
      'useFeedbackOverlay must be used within a FeedbackOverlayProvider',
    );
  }
  return context;
}
