import { getMetaApiData } from '@app/storage/local/metaApiData';
import { useEffect, useMemo, useState } from 'react';
import { versionStringToNumber } from '../helpers/versionStringToNumber';
import { browser_api } from '@app/browser_api';

type VersionData = {
  requiredVersion: number;
  currentVersion: number;
} | null;

export function useVersion(): { isVersionOld: boolean } {
  const [versionData, setVersionData] = useState<VersionData>(null);

  const isVersionOld = useMemo(
    () =>
      versionData
        ? versionData.currentVersion < versionData.requiredVersion
        : false,

    [versionData],
  );

  useEffect(() => {
    async function getVersionData(): Promise<void> {
      const currentVersion = versionStringToNumber(
        browser_api.runtime.getManifest().version,
      );
      const { requiredVersion } = await getMetaApiData();

      setVersionData({
        requiredVersion: versionStringToNumber(requiredVersion),
        currentVersion,
      });
    }

    void getVersionData();
  }, []);

  return {
    isVersionOld,
  };
}
