import { clsx } from 'clsx';
import { TelegramIcon } from './TelegramIcon';

import styles from './TelegramPromo.module.css';
import { useTranslation } from 'react-i18next';

type TelegramPromoProps = {
  className?: string;
  startParam?: string | null;
};

export function TelegramPromo({ className, startParam }: TelegramPromoProps): JSX.Element { 
  const { t } = useTranslation('shared', {
    keyPrefix: 'telegram-promo',
  });



  return (
    <a
      href={`https://t.me/${process.env.TELEGRAM_URL}${startParam ? `?start=${startParam}` : ''}`}
      target="_blank"
      className={className}
    >
      <button className={clsx(styles.container, styles.button, styles.size)}>
        <TelegramIcon className={styles.telegram} />
        <span>{t('channel')}</span>
      </button>
    </a>
  );
}
