import { Error } from '../../Error';
import { useConfigsBaseUrl } from 'src/utils/hooks';

type BrowserErrorProps = {
  onClick: () => void;
  header?: React.ReactNode;
  footer?: React.ReactNode;
};
export function BrowserError({
  onClick,
  header,
  footer,
}: BrowserErrorProps): JSX.Element {
  const { configsUrl } = useConfigsBaseUrl();

  const helpUrl = process.env.HELP_URL
    ? new URL(process.env.HELP_URL, configsUrl)
    : null;

  const isNoFAQ: boolean = process.env.IS_NO_FAQ === 'true' || !helpUrl;
  const title = process.env.EXTENSION_TITLE || '';
  const text = isNoFAQ
    ? `К сожалению, ${title} не поддерживает данный
  браузер. Вы можете продолжить пользоваться расширением, но его работа
  не гарантирована`
    : `К сожалению, ${title} не поддерживает данный
  браузер. Вы можете продолжить пользоваться расширением, но его работа
  не гарантирована. Для получения списка поддерживаемых браузеров и
  другой информации о работе расширения перейдите на страницу`;

  return (
    <Error
      text={text}
      textLink={
        isNoFAQ ? null : (
          <a href={helpUrl?.toString()} target="_blank" rel="noreferrer">
            FAQ
          </a>
        )
      }
      buttonText="Ok"
      onClick={onClick}
      header={header}
      footer={footer}
    />
  );
}
