import { useEffect, useState, type ReactElement } from 'react';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { extensionsMap } from './constants';

import styles from './ProxyControlledByOtherExtensions.module.css';


import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
type ProxyControlledByOtherExtensionsProps = {
  onClick: () => Promise<void>;
  children: ReactElement;
  conflictType: 'proxy' | 'adBlocker';
};

const DIAGNOSTIC_URL = process.env.DIAGNOSTIC_EXTENSIONS_URL;

export function ProxyControlledByOtherExtensions({
  onClick,
  children,
  conflictType,
}: ProxyControlledByOtherExtensionsProps): JSX.Element {
  const [dataMonitoring] = useLocalStorage([
    LocalStorageKey.ExtensionMonitoring,
  ]);
  const [name, setName] = useState<string | null>(null);
  const [fullName, setFullName] = useState<string | null>(null);
  const [icon, setIcon] = useState<string | null>(null);

  const extensionTitle = process.env.EXTENSION_TITLE ?? 'нашего расширения';
  const extensionCode = process.env.EXTENSION_CODE;

  useEffect(() => {
    if (!dataMonitoring?.[conflictType]?.hasConflictingExtension) {
      return;
    }

    const {
      conflictingExtensionIcon,
      conflictingExtensionId,
      conflictingExtensionName,
    } = dataMonitoring[conflictType];
    const extensionNameFromMap = conflictingExtensionId
      ? extensionsMap[conflictingExtensionId]
      : null;

    setIcon(conflictingExtensionIcon ?? null);
    setName(extensionNameFromMap ?? conflictingExtensionName ?? null);
    setFullName(conflictingExtensionName ?? null);
  }, [dataMonitoring, conflictType]);

  const handleLinkClick = async () => {
    await onClick();
    window.open(DIAGNOSTIC_URL, '_blank', 'noopener,noreferrer');
  };

  return (
    <div className={styles.container}>
      {children}
      <div className={styles.content}>
        <div className={styles.description}>
          {name ? (
            <>
              <h2 className={styles.title}>
                {icon && (
                  <img
                    src={icon}
                    alt="extension-icon"
                    className={styles.icon}
                  />
                )}
                <span>{name}</span>
                <br />
                <span>Блокирует работу {extensionTitle}</span>
              </h2>
              <p className={styles['warning-info-text']}>
                Пожалуйста, отключите расширение {fullName && `«${fullName}»`},
                оно мешает работе {extensionTitle}
              </p>
            </>
          ) : (
            <>
              <h2 className={styles.title}>
                Кажется, другие расширения мешают работе {extensionTitle}
              </h2>
              <p className={styles['warning-info-text']}>
                Мы заметили, что одно из расширений в вашем браузере блокирует
                работу нашего сервиса. Чтобы {extensionTitle} работал корректно,
                пожалуйста, временно отключите другие расширения, которые влияют
                на подключение.
              </p>
            </>
          )}
        </div>
        <div className={styles.buttons}>
          {extensionCode === 'youboost-neo' && (
            <button
              className={`${styles.button} ${styles.link} premium`}
              onClick={handleLinkClick}
            >
              Нужна помощь?
            </button>
          )}
          <button
            onClick={onClick}
            className={`${styles.button} premium`}
            id="proxy-controlled-btn"
          >
            Все понятно
          </button>
        </div>
      </div>
    </div>
  );
}
