import { httpClient } from '../httpClient';
import { getSetupTariffFreeApiUrl, type SetupTariffFreeResponse } from '../urls';
import { HTTPError } from 'ky';

export type SetupTariffFreeHttpError = {
  error: {
    code: string;
    localized_message: string;
    parameter_errors: [
      {
        field: string;
        errors: [
          {
            code: string;
            localized_message: string;
          },
        ];
      },
    ];
  };
};

export async function setupTariffFree(
  apiBaseUrl: string,
  {
    userId,
    deviceId,
    deviceIp,
  }: {
    userId?: string;
    deviceId?: string;
    deviceIp?: string;
  },
): Promise<SetupTariffFreeResponse | undefined> {
  const apiUrl = getSetupTariffFreeApiUrl(apiBaseUrl);
  try {
    const data = await httpClient
      .post<SetupTariffFreeResponse>(apiUrl, {
        json: {
          userId,
          deviceId,
          deviceIp,
        },
      })
      .json();
    return data;
  } catch (error) {
    console.error(error);
    if (error instanceof HTTPError) {
      const errorData = await error.response.json<SetupTariffFreeHttpError>();
      throw new Error(errorData.error.localized_message);
    }
    throw new Error('Что-то пошло не так.');
  }
}
