import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Actions } from '@app/actions/shared';
import { RoutePath } from '../routes/types/RoutePath';
import { browser_api } from '@app/browser_api';

export function useNavigateToUnavailableTrial(): void {
  const navigate = useNavigate();

  useEffect(() => {
    const handler = (message: any) => {
      if (message.action === Actions.NavigateToUnavailableTrial) {
        navigate(RoutePath.UnavailableTrial);
      }
    };
    browser_api.runtime.onMessage.addListener(handler);
    return () => {
      browser_api.runtime.onMessage.removeListener(handler);
    };
  }, [navigate]);
}
