import { createContext, useCallback, useMemo, useState } from 'react';
import {
  getLocalStorageData,

} from '@app/storage/local/shared';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { useFeaturesList } from '@app/features/hooks/useFeaturesList';
import { Feature } from '@app/features/types/Feature';


import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export type FeedbackOverlayContextType = {
  isFeedbackOverlayShown: boolean;
  checkAndShowFeedbackOverlay: () => Promise<void>;
  closeFeedbackOverlay: () => void;
};

export const FeedbackOverlayContext = createContext<
  FeedbackOverlayContextType | undefined
>(undefined);

export function FeedbackOverlayContextProvider({
  children,
}: {
  children: React.ReactNode;
}): JSX.Element {
  const [isFeedbackOverlayShown, setIsFeedbackOverlayShown] = useState(false);
  const [hasPremium] = useLocalStorage(LocalStorageKey.HasPremium, false);
  const { featuresList } = useFeaturesList();

  const canFreeUsersReview = useMemo(
    () => featuresList[Feature.FreeUsersReview],
    [featuresList],
  );

  const checkAndShowFeedbackOverlay = useCallback(async (): Promise<void> => {
    const { feedbackProvided, connectionCount } = await getLocalStorageData([
      LocalStorageKey.ConnectionCount,
      LocalStorageKey.FeedbackProvided,
    ]);

    const feedbackMilestones = [1, 2, 3, 5, 8, 13, 21, 34, 55];

    if (
      (hasPremium || canFreeUsersReview) &&
      !feedbackProvided &&
      connectionCount !== undefined &&
      feedbackMilestones.includes(connectionCount)
    ) {
      setIsFeedbackOverlayShown(true);
    }
  }, [hasPremium, canFreeUsersReview]);

  function closeFeedbackOverlay(): void {
    setIsFeedbackOverlayShown(false);
  }

  const value = useMemo(
    () => ({
      isFeedbackOverlayShown,
      checkAndShowFeedbackOverlay,
      closeFeedbackOverlay,
    }),
    [checkAndShowFeedbackOverlay, isFeedbackOverlayShown],
  );

  return (
    <FeedbackOverlayContext.Provider value={value}>
      {children}
    </FeedbackOverlayContext.Provider>
  );
}
