import { usePremiumAnimation } from '@popup/shared/hooks/usePremiumAnimation';
import { useState } from 'react';
import {
  Discount,
  Problem,
  Sorry,
  type CancelPage,
} from '../components/CancelPremium';

export function CancelPremium(): JSX.Element | null {
  const [page, setPage] = useState<CancelPage>('problem');

  usePremiumAnimation();

  return (
    <>
      <Problem
        page={page}
        onNext={() => {
          setPage('discount');
        }}
      />
      <Discount
        page={page}
        onNext={() => {
          setPage('sorry');
        }}
      />
      <Sorry page={page} />
    </>
  );
}
