import { PopUpContainer } from '@popup/shared/components/PopUpContainer';
import { useState } from 'react';
import { createPortal } from 'react-dom';
import { ExitPopUp } from '../../ExitPopUp';
import { Icon } from './Icon';

import styles from './ExitButton.module.css';
import { useTranslation } from 'react-i18next';

export function ExitButton(): JSX.Element {
  const { t } = useTranslation('shared', {
    keyPrefix: 'exit-button',
  });

  const popUp = document.getElementById('popup');
  const [isPopUpVisible, setIsPopUpVisible] = useState(false);

  const handleClick = () => {
    setIsPopUpVisible(true);
  };

  return (
    <>
      <button type="button" onClick={handleClick} className={styles.button}>
        <span className={styles.text}>{t('text')}</span>
        <Icon className={styles.icon} />
      </button>
      {!!popUp &&
        isPopUpVisible &&
        createPortal(
          <PopUpContainer onClose={() => setIsPopUpVisible(false)}>
            <ExitPopUp onClose={() => setIsPopUpVisible(false)} />
          </PopUpContainer>,
          popUp,
        )}
    </>
  );
}
