export type Message =
  | ShowVideoLoaderMessage
  | DirectVideoOpenMessage
  | FailedVideoOpenMessage
  | VideoOpenerReadyMessage
  | NetworkConnectionRetryMessage
  | NetworkConnectionRetrySuccessMessage
  | NetworkConnectionRetryFailMessage
  | OpenPopupMessage
  | NavigateToFreeTrialMessage
  | PopupReadyMessage
  | OpenPopupPaymentMessage
  | NavigateToPaymentMessage
  | SaveTimeMessage
  | OpenPopupChangeDeviceMessage
  | CreateNewUserMessage
  | CreateNewUserSuccessMessage
  | ShowGiftAdvertisingScreenMessage
  | InitGiftAdvertisingScriptMessage
  | GiftClickedMessage
  | InitGiftAdvertisingScriptReadyMessage
  | UserStatusChangedMessage
  | ConnectionStateChangedMessage
  | ConfigsBaseUrlMessage
  | HideGiftMessage
  | OpenPopupRootMessage;

export function isMessageOfType<T extends Message['action']>(
  msg: unknown,
  action: T,
): msg is Extract<Message, { action: T }> {
  return (
    typeof msg === 'object' &&
    msg !== null &&
    'action' in msg &&
    (msg as any).action === action
  );
}

export type ActionPayloadMap = {
  [Actions.UserStatusChanged]: UserStatusChangedMessage['payload'];
  [Actions.ConnectionStateChanged]: ConnectionStateChangedMessage['payload'];
  [Actions.ConfigsBaseUrl]: ConfigsBaseUrlMessage['payload'];
};
