import { httpClient } from '../httpClient';

import { PaymentMethod } from '@app/features/types/paymentMethodTypes';

type CreateNewPaymentLinkDto = {
  url: string;
  tariffName: string;
  paymentMethod: PaymentMethod;
  userId?: string;
};

type CreateNewPaymentLinkResponse = {
  paymentLink: string;
};

export async function createNewPaymentLink({
  url,
  userId,
  tariffName,
  paymentMethod,
}: CreateNewPaymentLinkDto): Promise<CreateNewPaymentLinkResponse> {
  const data = await httpClient
    .post<CreateNewPaymentLinkResponse>(url, {
      json: {
        userId,
        tariff: tariffName,
        paymentMethod,
      },
    })
    .json();
  return data;
}
