import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import {
  createContext,
  useCallback,
  useLayoutEffect,
  useMemo,
  useState,
} from 'react';
import { useNavigate } from 'react-router-dom';
import { RoutePath } from '../../routes/types/RoutePath';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';


import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export type ModeSwitcherContextType = {
  isYoutubeMode: boolean;
  hasPremium: boolean;
  onModeChange: (isYoutubeMode: boolean) => void;
};

const DEFAULT_VALUE = {
  isYoutubeMode: true,
  hasPremium: false,
  onModeChange: (isYoutubeMode: boolean) => {},
};

export const ModeSwitcherContext =
  createContext<ModeSwitcherContextType>(DEFAULT_VALUE);

export function ModeSwitcherContextProvider({
  children,
}: {
  children: React.ReactNode;
}) {
  const [isYoutubeMode, setIsYoutubeMode] = useState(true);

  const { hasPremium } = usePremiumData();

  const navigate = useNavigate();

  useLayoutEffect(() => {
    async function getModeValue() {
      const { isAllInternetProxy } = await getLocalStorageData(
        LocalStorageKey.IsAllInternetProxy,
      );

      setIsYoutubeMode(!isAllInternetProxy);
    }

    if (hasPremium) {
      getModeValue();
    }
  }, [hasPremium]);

  const handleModeChange = useCallback(
    async (isYoutubeMode: boolean) => {
      if (!hasPremium && !isYoutubeMode) {
        navigate(RoutePath.Tariffs);
        return;
      }

      await setLocalStorageData({
        [LocalStorageKey.IsAllInternetProxy]: !isYoutubeMode,
      });
      setIsYoutubeMode(isYoutubeMode);
    },
    [hasPremium, navigate],
  );

  const value = useMemo(
    () => ({
      isYoutubeMode,
      hasPremium: !!hasPremium,
      onModeChange: handleModeChange,
    }),
    [isYoutubeMode, hasPremium, handleModeChange],
  );

  return (
    <ModeSwitcherContext.Provider value={value}>
      {children}
    </ModeSwitcherContext.Provider>
  );
}
