import { useLayoutEffect } from 'react';

export function usePremiumAnimation(
  { isFreeUser } = { isFreeUser: true },
): void {
  useLayoutEffect(() => {
    if (isFreeUser) {
      document.body.classList.add('background-transition');
    }
    return () => {
      if (isFreeUser) {
        document.body.classList.remove('background-transition');
      }
    };
  }, [isFreeUser]);
}
