import { ActionButton } from '@popup/shared/components/ActionButton';
import { Link } from 'react-router-dom';
import { RoutePath } from '@popup/project/youboost-neo/routes/types/RoutePath';
import { useTranslation } from 'react-i18next';

type CancelPremiumPopUpProps = {
  onClose: () => void;
};
export function CancelPremiumPopUp({
  onClose,
}: CancelPremiumPopUpProps): JSX.Element {
  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'root.premium-data.cancel-premium',
  });

  return (
    <div>
      <h3>{t('title')}</h3>
      <Link to={RoutePath.CancelPremium} tabIndex={-1}>
        <ActionButton onClick={onClose}>{t('yes')}</ActionButton>
      </Link>
      <Link to={RoutePath.Tariffs} tabIndex={-1}>
        <ActionButton onClick={onClose}>{t('no')}</ActionButton>
      </Link>
    </div>
  );
}
