import React, { memo } from 'react';
import styles from './PublicOfferSection.module.css';
import { Trans, useTranslation } from 'react-i18next';

interface PublicOfferSectionProps {
  publicOfferUrl: string;
}

const RawPublicOfferSection: React.FC<PublicOfferSectionProps> = ({
  publicOfferUrl,
}) => {
  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'payment.public-offer',
  });

  return (
    <>
      <p className={styles['public-offer']}>
        <Trans t={t} i18nKey="title" />{' '}
        <a
          className={styles['public-offer-url']}
          href={publicOfferUrl}
          target="_blank"
        >
          {t('link-text')}
        </a>
      </p>
    </>
  );
};

export const PublicOfferSection = memo(RawPublicOfferSection);
