import { useEffect, useState } from 'react';
import { browser_api } from '@app/browser_api';
import type { Storage } from 'webextension-polyfill';
import {
  LocalStorageData,
  LocalStorageKeyType,
} from '@app/storage/local/types/sharedTypes';
import {
  setLocalStorageData,
  getLocalStorageData,
} from '@app/storage/local/shared';

type StorageValue<K> = K extends keyof LocalStorageData
  ? LocalStorageData[K]
  : never;

export function useLocalStorage<K extends LocalStorageKeyType>(
  key: K | K[],
  defaultValue?: StorageValue<K>,
) {
  const [value, setValue] = useState<StorageValue<K> | null>(
    defaultValue ?? null,
  );
  const storageKey = Array.isArray(key) ? key[0] : key;

  useEffect(() => {
    // Initial value
    getLocalStorageData(storageKey).then((result) => {
      setValue(result[storageKey] as StorageValue<K>);
    });

    // Listen for changes
    const listener = (changes: { [key: string]: Storage.StorageChange }) => {
      if (changes[storageKey]) {
        setValue(changes[storageKey].newValue as StorageValue<K>);
      }
    };

    browser_api.storage.local.onChanged.addListener(listener);

    return () => {
      browser_api.storage.local.onChanged.removeListener(listener);
    };
  }, [storageKey]);

  const set = async (newValue: StorageValue<K>) => {
    await setLocalStorageData({ [storageKey]: newValue });
  };

  return [value, set] as const;
}
