import { useFreePremiumTrial } from '@popup/project/youboost-neo/hooks/useFreePremiumTrial';
import { RoutePath } from '@popup/project/youboost-neo/routes/types/RoutePath';
import { Error } from '@popup/shared/components/errors/Error';
import { useNavigate } from 'react-router-dom';

import { PremiumPromo } from '../../PremiumPromo';
import { Footer } from '../../shared/Footer';
import { Loader } from '../../shared/Loader';

import styles from './PremiumInfoContent.module.css';
import { useTranslation } from 'react-i18next';

export function PremiumInfoContent(): JSX.Element {
  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'premium-info',
  });

  const {
    isCheckingFreePremiumTrial,
    canTryFreePremiumTrial,
    isFreePremiumTrialError,
    isFreePremiumTrialLoading,
    handleClickTryPremium,
  } = useFreePremiumTrial();

  const navigate = useNavigate();

  if (isFreePremiumTrialError) {
    return <Error />;
  }

  if (isFreePremiumTrialLoading) {
    return <Loader className={styles.loader}>{t('loader-text')}</Loader>;
  }

  if (isCheckingFreePremiumTrial) {
    return <Loader className={styles['check-loader']} />;
  }

  if (canTryFreePremiumTrial === false) {
    navigate(RoutePath.Tariffs);
  }

  return (
    <>
      <PremiumPromo handleClickTryPremium={handleClickTryPremium} />
      <Footer />
    </>
  );
}
