import { detect } from 'detect-browser';
import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { browser_api } from '@app/browser_api';
import { getLocalStorageData } from '@app/storage/local/shared';

type DebugButtonHookValues = {
  isCopied: boolean;
  handleCopy: () => void;
};

// TODO remove this function after the backend logic will be implemented
function reducePacScript(
  storageData: Record<string, unknown>,
): Record<string, unknown> {
  const { pacPremiumData, pacData, ...rest } = storageData;

  return {
    ...rest,
    pacData: {
      updatedAt: (pacData as Record<string, unknown>)?.updatedAt,
    },
    pacPremiumData: pacPremiumData
      ? {
          updatedAt: (pacPremiumData as Record<string, unknown>)?.updatedAt,
        }
      : undefined,
  };
}

export function useDebugButton(): DebugButtonHookValues {
  const [isCopied, setIsCopied] = useState(false);
  const timeoutRef = useRef<NodeJS.Timeout | null>(null);

  const handleGetDebugInfo = useCallback(async () => {
    try {
      setIsCopied(true);
      const browserInfo = detect();
      const extensionInfo = {
        version: browser_api.runtime.getManifest().version,
      };
      const platformInfo = await browser_api.runtime.getPlatformInfo();
      const storageData = {
        local: reducePacScript(await getLocalStorageData()),
      };

      const debugData = JSON.stringify(
        {
          browserInfo,
          extensionInfo,
          platformInfo,
          storageData,
          chromeRuntimeLastError: browser_api.runtime.lastError?.message,
        },
        null,
        2,
      );

      const encodedDebugData = btoa(encodeURIComponent(debugData));

      await navigator.clipboard.writeText(encodedDebugData);

      return encodedDebugData;
    } catch (error) {
      console.error('Error gathering debug information:', error);
    } finally {
      setIsCopied(true);

      timeoutRef.current = setTimeout(() => {
        setIsCopied(false);
      }, 1000);
    }
  }, []);

  useEffect(() => {
    return () => {
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current);
      }
    };
  }, []);

  return useMemo(
    () => ({
      isCopied,
      handleCopy: handleGetDebugInfo,
    }),
    [handleGetDebugInfo, isCopied],
  );
}
