import { useEffect, useRef } from 'react';
import { browser_api } from '@app/browser_api';
import { PopupReadyMessage } from '@app/messages/shared';
import { Actions } from '@app/actions/shared';

export function usePopupReady(): void {
  const isMessageSent = useRef(false);

  useEffect(() => {
    if (!isMessageSent.current) {
      void browser_api.runtime.sendMessage<PopupReadyMessage>({
        action: Actions.PopupReady,
      });
      isMessageSent.current = true;
    }
  });
}
