import { setupTariffFree } from '@api/premium/setupTariffFree';
import { getMetaApiData } from '@app/storage/local/metaApiData';
import { getDeviceId, getDeviceIP } from '@background/client/utils';
import { useState } from 'react';
import { usePaymentStatus } from './usePaymentStatus';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export const useSetupTariffFree = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [isError, setIsError] = useState(false);
  const [isSuccess, setIsSuccess] = useState(false);
  const paymentStatus = usePaymentStatus();

  async function handleClickBecomeFreeUser() {
    setIsLoading(true);
    const [deviceId, deviceIp, { userId }, { apiBaseUrl }] = await Promise.all([
      getDeviceId(),
      getDeviceIP(),
      getLocalStorageData(LocalStorageKey.UserId),
      getMetaApiData(),
    ]);
    try {
      const response = await setupTariffFree(apiBaseUrl, {
        deviceId,
        deviceIp,
        userId,
      });
      await setLocalStorageData({ hasPremium: false });
      await setLocalStorageData({
        status: response?.data?.subscription?.status || paymentStatus,
      });
      setIsSuccess(true);
    } catch (error) {
      console.error(error);
      setIsError(true);
    } finally {
      setIsLoading(false);
    }
  }

  return {
    handleClickBecomeFreeUser,
    isLoading,
    isError,
    isSuccess,
  };
};
