import React from 'react';
import clsx from 'clsx';
import { motion } from 'motion/react';
import styles from './ModeSwitcherButton.module.css';
import { ModeSwitcherButtonBlockIcon } from './ModeSwitcherButtonBlockIcon';

interface ModeSwitcherButtonProps {
  label: string;
  isActive: boolean;
  onClick: () => void;
  disabled?: boolean;
}

export const ModeSwitcherButton: React.FC<ModeSwitcherButtonProps> = ({
  label,
  isActive,
  disabled,
  onClick,
}) => {
  return (
    <motion.button
      whileTap={disabled ? undefined : { scale: 0.98 }}
      className={clsx(styles.button, {
        [styles['active-button']]: isActive,
        [styles['disabled-button']]: disabled,
      })}
      onClick={onClick}
    >
      {label}
      {disabled && <ModeSwitcherButtonBlockIcon />}
    </motion.button>
  );
};
