import { RoutePath } from '@popup/project/youboost-neo/routes/types/RoutePath';
import { Button } from '@popup/shared/components/Button';
import { Error } from '@popup/shared/components/errors/Error';
import { useSetupTariffFree } from '@popup/shared/hooks/useSetupTariffFree';
import { useEffect } from 'react';
import { Trans, useTranslation } from 'react-i18next';
import { Link, useNavigate } from 'react-router-dom';
import { Footer } from '../../shared/Footer';
import { SaleLogo } from '../../shared/SaleLogo';
import { Separator } from '../../shared/Separator/Separator';

import styles from './PremiumSalePersuasionContent.module.css';

export const PremiumSalePersuasionContent = () => {
  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'premium-sale-persuasion',
  });

  const navigate = useNavigate();

  const { handleClickBecomeFreeUser, isLoading, isSuccess, isError } =
    useSetupTariffFree();

  useEffect(() => {
    if (isSuccess) {
      navigate(RoutePath.Root, { replace: true });
    }
  }, [isSuccess, navigate]);

  if (isError) {
    return <Error text={t('error')} />;
  }

  return (
    <div className={styles.container}>
      <div className={styles.content}>
        <div className={styles.saleLogo}>
          <SaleLogo />
        </div>
        <div className={styles.saleTextContainer}>
          <h3 className={styles.saleTextTitle}>{t('title')}</h3>
          <p className={styles.saleTextDescription}>
            <Trans t={t} i18nKey="description" />
          </p>
        </div>
      </div>
      <div className={styles.buttons}>
        <Link
          to={RoutePath.Tariffs}
          state={{ hasOneMonthDiscount: true }}
          tabIndex={-1}
        >
          <Button className={styles.button} variant="primary">
            {t('go-premium')}
          </Button>
        </Link>
        <Separator />
        <Button
          className={styles.button}
          variant="secondary"
          onClick={handleClickBecomeFreeUser}
          disabled={isLoading}
          loading={isLoading}
        >
          {t('go-free')}
        </Button>
      </div>
      <Footer />
    </div>
  );
};
